import React, { Component, useState, useEffect, useContext } from 'react';

import './ChessControl.css';

import arrowDownFat from '../../../../assets/small-arrow-down-fat.svg';
import arrowDown from '../../../../assets/small-arrow-down.svg';

import el from '../../../../assets/chess/el.svg';
import bullet from '../../../../assets/chess/bullet.svg';
import clock from '../../../../assets/chess/clock.svg';
import time from '../../../../assets/chess/time.svg';

import { SocketContext } from '../../../../context/socketChess';

const ChessControl = (props) => {

    const [open, setOpen] = useState(false);

    const [player, setPlayer] = useState(1);
    const [mode, setMode] = useState({ img: el, title: '5 MIN', command: '5/00' });

    const [ready, setReady] = useState(false);

    const [search, setSearch] = useState(false);

    var modes = [
        {
            id: 0,
            img: bullet,
            options: [
                {
                    id: 0,
                    command: '1/00',
                    title: '1 MIN',
                },
                {
                    id: 1,
                    command: '1/30',
                    title: '1 MIN',
                },
                {
                    id: 2,
                    command: '2/00',
                    title: '2 MIN',
                },
            ]
        },
        {
            id: 1,
            img: el,
            options: [
                {
                    id: 0,
                    command: '3/00',
                    title: '3 MIN',
                },
                {
                    id: 1,
                    command: '4/00',
                    title: '4 MIN',
                },
                {
                    id: 2,
                    command: '5/00',
                    title: '5 MIN',
                },
            ]
        },
        {
            id: 2,
            img: clock,
            options: [
                {
                    id: 0,
                    command: '10/00',
                    title: '10 MIN',
                },
                {
                    id: 1,
                    command: '20/00',
                    title: '20 MIN',
                },
                {
                    id: 2,
                    command: '30/00',
                    title: '30 MIN',
                },
            ]
        },

    ]

    const socket = useContext(SocketContext);

    useEffect(() => {
        
        if (!props.id) {
            props.changePlayer(player);
            props.changeMode(mode.command);
        }

        socket.on('joined', () => {
            setReady(true);
            socket.emit('changePlayer', (player === 1 ? 2 : 1));
            socket.emit('changeMode', mode.command);
        });

        socket.on('left', () => {
            setReady(false);
        });

        return () => {
            
        }
    }, [player, mode]);


    const handleOpenClick = () => {
        if (!search) {
            setOpen(!open);
        }
    }

    const handleStartGameClick = () => {
        if (props.play === 'vsfriend' && ready) {
            socket.emit('startGame');
        }
        else if (props.play === 'online') {
            if (search) {
                socket.emit('cancelSearch');
                setSearch(false);
            }
            else {
                socket.emit('playOnline', mode.command);
                setSearch(true);
                setOpen(false);
            }
        }
    }

    return (
        // <div id="gamecontrolcont" style={(props.play === 'vsfriend' ? {marginRight: '100px'} : {})}>
        <div id="gamecontrolcont" style={(props.play === 'online' ? {marginRight: '0px'} : {})}>

            <div id="modecont" style={(open ? {height: '320px'} : {})} className={(search ? 'disabled' : '')}>
                <div id="modeshowcase">
                    <div id="modeimg">
                        <img src={ mode.img } alt="" />
                    </div>
                    <div id="modetime">
                        <label>{ mode.title }</label>
                    </div>
                    <div id="modedrop" onClick={ handleOpenClick }>
                        <img src={ arrowDownFat } alt="" style={(open ? {transform: 'rotate(-180deg)'} : {})} />
                    </div>
                </div>

                <div id="modeoptioncont">
                    {modes.map((modee) => {
                        return (
                            <div key={ modee.id } className="moption">
                                <div className="moimg">
                                    <img src={ modee.img } alt="" />
                                </div>
                                <div className="mocont">
                                {modee.options.map((option) => {
                                    let tempOpt = {img: modee.img, title: option.title, command: option.command};
                                    return (
                                        <div key={ option.id } className={"mooption " + (option.command === mode.command ? 'active' : '')} onClick={() => {setMode(tempOpt); props.changeMode(option.command); socket.emit('changeMode', option.command)}}>
                                            <label>{ option.title }</label>
                                        </div>
                                    )
                                })}
                                </div>
                            </div>
                        )
                    })}
                </div>

            </div>
            <div id="colorcont" style={(props.play === 'online' ? {display: 'none'} : {})}>
                <div id="whitebtn" className={"colorbtn " + (player === 1 ? 'active' : '')} onClick={() => {setPlayer(1); props.changePlayer(1); if (ready) {socket.emit('changePlayer', 2)}}}>
                    <label>White</label>
                </div>
                <div id="blackbtn" className={"colorbtn " + (player === 2 ? 'active' : '')} onClick={() => {setPlayer(2); props.changePlayer(2); if (ready) {socket.emit('changePlayer', 1)}}}>
                    <label>Black</label>
                </div>
            </div>
            <button id="playbutton" className={(ready ? 'ready ' : '') + (props.play === 'online' ? 'ready ' : '')} style={(props.play === 'online' ? {marginTop: '20px'} : {})} onClick={ handleStartGameClick } >{(props.play === 'online' ? (!search ? 'Start search' : 'Cancel search') : 'Start game')}</button>

        </div>
    );
}
 
export default ChessControl;




