import Piece from "./Piece";

import King from "./King";

import bishopWhite from '../../../../assets/chess/bishop-white.svg'
import bishopBlack from '../../../../assets/chess/bishop-black.svg'

class Bishop extends Piece {
    constructor(player) {
        super(player, (player === 1 ? bishopWhite : bishopBlack));
        this.initialPos = {
            1: [2, 5],
            2: [58, 61]
        }
    }

    getPossibleMoves(startI, squares) {

        let possiblePos = []

        const row = startI[0];
        const col = startI[1];

        let ind = 1;
        while (row - ind >= 0 && col - ind >= 0) {

            if (squares[row - ind][col - ind] === null) {
                possiblePos.push(`${row - ind},${col - ind}`);
            }
            else if (squares[row - ind][col - ind] !== null) {
                if (squares[row - ind][col - ind].player !== this.player) {
                    possiblePos.push(`${row - ind},${col - ind}`);
                }
                break;
            }

            ind++;
        }

        ind = 1;
        while (row - ind >= 0 && col + ind < 8) {

            if (squares[row - ind][col + ind] === null) {
                possiblePos.push(`${row - ind},${col + ind}`);
            }
            else if (squares[row - ind][col + ind] !== null) {
                if (squares[row - ind][col + ind].player !== this.player) {
                    possiblePos.push(`${row - ind},${col + ind}`);
                }
                break;
            }

            ind++;
        }

        ind = 1;
        while (row + ind < 8 && col - ind >= 0) {

            if (squares[row + ind][col - ind] === null) {
                possiblePos.push(`${row + ind},${col - ind}`);
            }
            else if (squares[row + ind][col - ind] !== null) {
                if (squares[row + ind][col - ind].player !== this.player) {
                    possiblePos.push(`${row + ind},${col - ind}`);
                }
                break;
            }

            ind++;
        }

        ind = 1;
        while (row + ind < 8 && col + ind < 8) {

            if (squares[row + ind][col + ind] === null) {
                possiblePos.push(`${row + ind},${col + ind}`);
            }
            else if (squares[row + ind][col + ind] !== null) {
                if (squares[row + ind][col + ind].player !== this.player) {
                    possiblePos.push(`${row + ind},${col + ind}`);
                }
                break;
            }

            ind++;
        }

        return possiblePos;

    }

    getHitPossibleMoves(startI, squares) {

        let possiblePos = []

        const row = startI[0];
        const col = startI[1];

        let ind = 1;
        while (row - ind >= 0 && col - ind >= 0) {

            if (squares[row - ind][col - ind] === null) {
                possiblePos.push(`${row - ind},${col - ind}`);
            }
            else if (squares[row - ind][col - ind] !== null) {
                possiblePos.push(`${row - ind},${col - ind}`);
                if (!(squares[row - ind][col - ind] instanceof King)) {
                    break;
                }
            }

            ind++;
        }

        ind = 1;
        while (row - ind >= 0 && col + ind < 8) {

            if (squares[row - ind][col + ind] === null) {
                possiblePos.push(`${row - ind},${col + ind}`);
            }
            else if (squares[row - ind][col + ind] !== null) {
                possiblePos.push(`${row - ind},${col + ind}`);
                if (!(squares[row - ind][col + ind] instanceof King)) {
                    break;
                }
            }

            ind++;
        }

        ind = 1;
        while (row + ind < 8 && col - ind >= 0) {

            if (squares[row + ind][col - ind] === null) {
                possiblePos.push(`${row + ind},${col - ind}`);
            }
            else if (squares[row + ind][col - ind] !== null) {
                possiblePos.push(`${row + ind},${col - ind}`);
                if (!(squares[row + ind][col - ind] instanceof King)) {
                    break;
                }
            }

            ind++;
        }

        ind = 1;
        while (row + ind < 8 && col + ind < 8) {

            if (squares[row + ind][col + ind] === null) {
                possiblePos.push(`${row + ind},${col + ind}`);
            }
            else if (squares[row + ind][col + ind] !== null) {
                possiblePos.push(`${row + ind},${col + ind}`);
                if (!(squares[row + ind][col + ind] instanceof King)) {
                    break;
                }
            }

            ind++;
        }

        return possiblePos;

    }

    getHitPartPossibleMoves(startI, squares) {

        let possiblePos = []

        const row = startI[0];
        const col = startI[1];

        let ind = 1;
        while (row - ind >= 0 && col - ind >= 0) {

            if (squares[row - ind][col - ind] === null) {
                possiblePos.push(`${row - ind},${col - ind}`);
            }
            else if (squares[row - ind][col - ind] !== null) {
                possiblePos.push(`${row - ind},${col - ind}`);
                break;
            }

            ind++;
        }

        ind = 1;
        while (row - ind >= 0 && col + ind < 8) {

            if (squares[row - ind][col + ind] === null) {
                possiblePos.push(`${row - ind},${col + ind}`);
            }
            else if (squares[row - ind][col + ind] !== null) {
                possiblePos.push(`${row - ind},${col + ind}`);
                break;
            }

            ind++;
        }

        ind = 1;
        while (row + ind < 8 && col - ind >= 0) {

            if (squares[row + ind][col - ind] === null) {
                possiblePos.push(`${row + ind},${col - ind}`);
            }
            else if (squares[row + ind][col - ind] !== null) {
                possiblePos.push(`${row + ind},${col - ind}`);
                break;
            }

            ind++;
        }

        ind = 1;
        while (row + ind < 8 && col + ind < 8) {

            if (squares[row + ind][col + ind] === null) {
                possiblePos.push(`${row + ind},${col + ind}`);
            }
            else if (squares[row + ind][col + ind] !== null) {
                possiblePos.push(`${row + ind},${col + ind}`);
                break;
            }

            ind++;
        }

        return possiblePos;

    }

}


export default Bishop;
