import Piece from "./Piece";

import King from "./King";

import rookWhite from '../../../../assets/chess/rook-white.svg'
import rookBlack from '../../../../assets/chess/rook-black.svg'

class Rook extends Piece {
    constructor(player) {
        super(player, (player === 1 ? rookWhite : rookBlack));
        this.initialPos = {
            1: [0, 7],
            2: [56, 63]
        }
    }

    getPossibleMoves(startI, squares) {

        let possiblePos = []

        const row = startI[0];
        const col = startI[1];

        let ind = 1;
        while (row - ind >= 0) {

            if (squares[row - ind][col] === null) {
                possiblePos.push(`${row - ind},${col}`);
            }
            else if (squares[row - ind][col] !== null) {
                if (squares[row - ind][col].player !== this.player) {
                    possiblePos.push(`${row - ind},${col}`);
                }
                break;
            }

            ind++;
        }
        
        ind = 1;
        while (row + ind < 8) {

            if (squares[row + ind][col] === null) {
                possiblePos.push(`${row + ind},${col}`);
            }
            else if (squares[row + ind][col] !== null) {
                if (squares[row + ind][col].player !== this.player) {
                    possiblePos.push(`${row + ind},${col}`);
                }
                break;
            }

            ind++;
        }

        ind = 1;
        while (col - ind >= 0) {

            if (squares[row][col - ind] === null) {
                possiblePos.push(`${row},${col - ind}`);
            }
            else if (squares[row][col - ind] !== null) {
                if (squares[row][col - ind].player !== this.player) {
                    possiblePos.push(`${row},${col - ind}`);
                }
                break;
            }

            ind++;
        }

        ind = 1;
        while (col + ind < 8) {

            if (squares[row][col + ind] === null) {
                possiblePos.push(`${row},${col + ind}`);
            }
            else if (squares[row][col + ind] !== null) {
                if (squares[row][col + ind].player !== this.player) {
                    possiblePos.push(`${row},${col + ind}`);
                }
                break;
            }

            ind++;
        }
            

        // console.log(possiblePos)
        return possiblePos;

    }
    
    getHitPossibleMoves(startI, squares) {

        let possiblePos = []

        const row = startI[0];
        const col = startI[1];

        let ind = 1;
        while (row - ind >= 0) {

            if (squares[row - ind][col] === null) {
                possiblePos.push(`${row - ind},${col}`);
            }
            else if (squares[row - ind][col] !== null) {
                possiblePos.push(`${row - ind},${col}`);
                if (!(squares[row - ind][col] instanceof King)) {
                    break;
                }
            }

            ind++;
        }
        
        ind = 1;
        while (row + ind < 8) {

            if (squares[row + ind][col] === null) {
                possiblePos.push(`${row + ind},${col}`);
            }
            else if (squares[row + ind][col] !== null) {
                possiblePos.push(`${row + ind},${col}`);
                if (!(squares[row + ind][col] instanceof King)) {
                    break;
                }
            }

            ind++;
        }

        ind = 1;
        while (col - ind >= 0) {

            if (squares[row][col - ind] === null) {
                possiblePos.push(`${row},${col - ind}`);
            }
            else if (squares[row][col - ind] !== null) {
                possiblePos.push(`${row},${col - ind}`);
                if (!(squares[row][col - ind] instanceof King)) {
                    break;
                }
            }

            ind++;
        }

        ind = 1;
        while (col + ind < 8) {

            if (squares[row][col + ind] === null) {
                possiblePos.push(`${row},${col + ind}`);
            }
            else if (squares[row][col + ind] !== null) {
                possiblePos.push(`${row},${col + ind}`);
                if (!(squares[row][col + ind] instanceof King)) {
                    break;
                }
            }

            ind++;
        }
            

        // console.log(possiblePos)
        return possiblePos;

    }

    getHitPartPossibleMoves(startI, squares) {

        let possiblePos = []

        const row = startI[0];
        const col = startI[1];

        let ind = 1;
        while (row - ind >= 0) {

            if (squares[row - ind][col] === null) {
                possiblePos.push(`${row - ind},${col}`);
            }
            else if (squares[row - ind][col] !== null) {
                possiblePos.push(`${row - ind},${col}`);
                break;
            }

            ind++;
        }
        
        ind = 1;
        while (row + ind < 8) {

            if (squares[row + ind][col] === null) {
                possiblePos.push(`${row + ind},${col}`);
            }
            else if (squares[row + ind][col] !== null) {
                possiblePos.push(`${row + ind},${col}`);
                break;
            }

            ind++;
        }

        ind = 1;
        while (col - ind >= 0) {

            if (squares[row][col - ind] === null) {
                possiblePos.push(`${row},${col - ind}`);
            }
            else if (squares[row][col - ind] !== null) {
                possiblePos.push(`${row},${col - ind}`);
                break;
            }

            ind++;
        }

        ind = 1;
        while (col + ind < 8) {

            if (squares[row][col + ind] === null) {
                possiblePos.push(`${row},${col + ind}`);
            }
            else if (squares[row][col + ind] !== null) {
                possiblePos.push(`${row},${col + ind}`);
                break;
            }

            ind++;
        }
            

        // console.log(possiblePos)
        return possiblePos;

    }

}


export default Rook;
