import React, { Component, useRef } from 'react';
import Bishop from '../Pieces/Bishop';
import Pawn from '../Pieces/Pawn';

import './ChessBoard.css';

import { isMobile } from 'react-device-detect';

const Square = (props) => {
    let cord = {0: 'a', 1: 'b', 2: 'c', 3: 'd', 4: 'e', 5: 'f', 6: 'g', 7: 'h'};

    // if (props.hover) {
    //     return (
    //         <div key={ props.id } className={ "square " +  props.color + ' ' + (props.i === props.hover[0] && props.j === props.hover[1] ? 'hover ' : '') }>
    //             {/* {(props.player === 1 ? cord[props.j] : cord[7 - props.j]) + ((props.player === 1 ? 8 - props.i : props.i + 1))} */}
    //             {(props.img ? <div key={props.id} className={"piece " + (props.piece.player === props.player ? "you " : "opponent ") + (props.piece instanceof Pawn ? "pawn " : "")  + (props.piece instanceof Bishop ? "bishop " : "")} style={ props.style }></div> : "")}
    //         </div>
    //     )
    // }
    // else {

    return (
        <div key={ props.id } className={ "square " + props.color + ' ' + (props.hover && props.i === props.hover[0] && props.j === props.hover[1] ? 'hover ' : '') + (props.checked && props.i === props.kingYou[0] && props.j === props.kingYou[1] ? 'checked ' : '') + (isMobile && props.selected && props.selected[0] === props.ind[0] && props.selected[1] === props.ind[1] ? 'selected ' : '') }>
            {/* {(props.player === 1 ? cord[props.j] : cord[7 - props.j]) + ((props.player === 1 ? 8 - props.i : props.i + 1))} */}
            {(props.img ? <div key={props.id} className={"piece " + (props.piece.player === props.player ? "you " : "opponent ") + (props.piece instanceof Pawn ? "pawn " : "")  + (props.piece instanceof Bishop ? "bishop " : "")} style={ props.style }></div> : "")}
        </div>
    )
    // }
}

class ChessBoard extends React.Component {

    renderSquare(i, j, color, player) {
        return (
            <Square
                key={ i + "-" + j }
                id = { i + "" + j }
                i = { i }
                j = { j }
                ind = {[i, j]}
                squares = { this.props.squares }
                piece = { this.props.squares[i][j] }
                color = { color }
                player = { player }
                img = { this.props.squares[i][j] ? this.props.squares[i][j].img : null }
                style = { this.props.squares[i][j] ? this.props.squares[i][j].style : null}
                hover = { this.props.activePieceHover }
                selected = { this.props.activePiecePos }
                checked = { this.props.checked }
                kingYou = { this.props.kingYou }
            />
        )
    }

    render() {
        const board = [];

        for (let i = 0; i < 8; i++) {
            const row = [];
            for (let j = 0; j < 8; j++) {
                const color = (isEven(i) && isEven(j)) || (!isEven(i) && !isEven(j))? "light" : "dark";
                row.push(this.renderSquare(i, j, color, this.props.player));
            }
            board.push(row);
        }

        if (isMobile) {
            return (
                <div id="chessboard" className={(this.props.gay ? 'gay ' : '')} onClick={(e) => this.props.handleClick(e)}>
                    { board }
                </div>
            );
        }
        else {
            return (
                <div id="chessboard" className={(this.props.gay ? 'gay ' : '')} onMouseDown = {(e) => this.props.onMouseDown(e)} onMouseMove = {(e) => this.props.onMouseMove(e)} onMouseUp = {(e) => this.props.onMouseUp(e)}>
                    { board }
                </div>
            );
        }
    }
}

function isEven(num){
    return num % 2 == 0
}

export default ChessBoard;

