import React, { Component, useState } from 'react';

import ScrollContainer from 'react-indiana-drag-scroll';

import './GamesPage.css';

import GameCategory from '../GameCategory/GameCategory';

import chessimg from '../../assets/chessimg.svg';
import arrowDown from '../../assets/arrow-down.svg';

const GamesPage = () => {

    const options = [
        {
            id: 1,
            title: 'Popular Games',
            games: [
                {
                    id: 1.1,
                    title: 'Chess',
                    to: '/Chess',
                    img: chessimg,
                },
                {
                    id: 1.2,
                    title: 'Sudoku',
                    to: '/Sudoku',
                    img: chessimg,
                },
                {
                    id: 1.3,
                    title: 'TTT',
                    to: '/TTT',
                    img: chessimg,
                },
                {
                    id: 1.4,
                    title: 'Memo',
                    to: '/Memo',
                    img: chessimg,
                },

            ]
        },
        {
            id: 2,
            title: 'Multiplayer Games',
            games: [
                {
                    id: 2.1,
                    title: 'Chess',
                    to: '/Chess',
                    img: chessimg,
                },
                {
                    id: 2.2,
                    title: 'TTT',
                    to: '/TTT',
                    img: chessimg,
                },
            ]
        },
        {
            id: 3,
            title: 'Singleplayer Games',
            games: [
                {
                    id: 3.1,
                    title: 'Sudoku',
                    to: '/Sudoku',
                    img: chessimg,
                },
                {
                    id: 3.2,
                    title: 'Memo',
                    to: '/Memo',
                    img: chessimg,
                },

            ]
        },
    ]
    
    return (
        <div id="gamespagecont">

            {options.map((option) => {
                return( 
                    <GameCategory key={ option.id } props={ option } />
                )
            })}

        </div>
    );
}
 
export default GamesPage;




