import React, { Component, useState, useEffect, useContext } from 'react';

import { useHistory } from 'react-router-dom';

import './ChessConnect.css';
import ChessControl from './ChessControl/ChessControl';

import { SocketContext } from '../../../context/socketChess';
import ChessInvite from './ChessInvite/ChessInvite';

const ChessConnect = (props) => {

    const [windowWidth, setWindowWidth] = useState(window.innerWidth);
    const handleResize = () => {
        setWindowWidth(window.innerWidth);
    }
    window.addEventListener('resize', handleResize)

    let history = useHistory();

    const [play, setPlay] = useState('online');

    const socket = useContext(SocketContext);

    useEffect(() => {
        
        if (props.id) {
            socket.emit('join', props.id);
        }
    
        socket.on('changePlayer', (player) => {
            props.changePlayer(player);
        });
        socket.on('changeMode', (mode) => {
            props.changeMode(mode);
        });
        socket.on('startGame', () => {
            // setTimeout(() => {
            //     props.startGame();
            // }, 1000);
            props.startGame();
        });

        socket.on('left', () => {
            if (props.id) {
                history.push('/Chess');
            }
        });

        return () => {
            
        }
    }, [])


    if (!props.id) {
        return (
            <div id="chessconnect">
                <div id="cctop">
                    <div id="cctleft" className={"ccoption " + (play === 'online' ? 'active' : '')} onClick={() => setPlay('online')}>
                        <label>Online</label>
                    </div>
    
                    <div id="cctright" className={"ccoption " + (play === 'vsfriend' ? 'active' : '')} onClick={() => setPlay('vsfriend')}>
                        <label>Vs Friend</label>
                    </div>
                </div>
                
                {play === 'vsfriend' ? (
                    // <div id="ccbottomcont">
                        <div id="ccbottom">
                            <ChessControl play={ play } id={ props.id } changePlayer={ props.changePlayer} changeMode={ props.changeMode } />
                            <ChessInvite />
                        </div>
                    // </div>
                ) : play === 'online' ? (
                    <div id="ccbottom">
                        <ChessControl play={ play } id={ null } changePlayer={ props.changePlayer} changeMode={ props.changeMode } />
                    </div>
                ) : <div id="ccbottom"></div>}

                <div id='mobilemodebar'>
                    <div id='mmbleft' className={"mmboption " + (play === 'online' ? 'active' : '')} onClick={() => setPlay('online')}>
                        Online
                    </div>
                    <div id='mmbright' className={"mmboption " + (play === 'vsfriend' ? 'active' : '')} onClick={() => setPlay('vsfriend')}>
                        Vs Friend
                    </div>
                </div>

            </div>
        );    
    }
    else {
        return (
            <div id="chesswait">
                <div>
                    <h1>WAITING FOR HOST TO START THE GAME</h1>
                    <button onClick={ props.goToMenu }>LEAVE</button>
                </div>
            </div>
        )
    }
}
 
export default ChessConnect;


