import React, { Component, useState, useEffect, useContext } from 'react';

import { useHistory } from 'react-router-dom';

import './TTTConnect.css';

import TTTControl from './TTTControl/TTTControl';
import TTTInvite from './TTTInvite/TTTInvite';

import { SocketContext } from '../../../context/socketTTT';

import DrawSpot from './TTTControl/DrawSpot/DrawSpot';

import x from '../../../assets/ttt/x.svg';
import o from '../../../assets/ttt/o.svg';

const TTTConnect = (props) => {

    const [windowWidth, setWindowWidth] = useState(window.innerWidth);
    const handleResize = () => {
        setWindowWidth(window.innerWidth);
    }
    window.addEventListener('resize', handleResize)

    let history = useHistory();

    const [play, setPlay] = useState('online');

    const socket = useContext(SocketContext);


    useEffect(() => {
        
        if (props.id) {
            socket.emit('join', props.id);
        }

        //////////////// ihouAFSGYDDDDDDDDDDDDDD //////////////////
        socket.on('changeShape', (shape) => {
            if (props.shape === x || props.shape === o || props.shape === null) {
                props.changeShape(shape);
            }
        })

        socket.on('changeOppShape', (shape) => {
            props.changeOppShape(shape);
        })

        socket.on('changePlayer', (player) => {
            props.changePlayer(player);
        })

        socket.on('changeData', (shape, player) => {
            props.changePlayer(player);
            props.changeShape((shape ? shape : player === 1 ? x : o));
            socket.emit('changeOppShape', (shape ? shape : player === 1 ? o : x));
        });
        
        socket.on('startGame', () => {
            socket.emit('changeOppShape', props.shape);
            setTimeout(() => {
                props.startGame();
            }, 1000)
        })

        return () => {
            
        }
    }, [props.shape]);


    const handleCustomShapeChange = (shape) => {
        props.changeShape(shape);
        
        socket.emit('changeOppShape', shape);
    }

    if (!props.id) {
        return (
            <div id="tttconnect">
                <div id="cctop">
                    <div id="cctleft" className={"ccoption " + (play === 'online' ? 'active' : '')} onClick={() => setPlay('online')}>
                        <label>Online</label>
                    </div>
    
                    <div id="cctright" className={"ccoption " + (play === 'vsfriend' ? 'active' : '')} onClick={() => setPlay('vsfriend')}>
                        <label>Vs Friend</label>
                    </div>
                </div>
                
                {play === 'vsfriend' ? (
                    // <div id="ccbottomcont">
                        <div id="ccbottom">
                            <TTTControl play={ play } id={ props.id } changeShape={ props.changeShape} changeOppShape={ props.changeOppShape } changePlayer={ props.changePlayer } oppShape={ props.oppShape } />
                            <TTTInvite />
                        </div>
                    // </div>
                ) : play === 'online' ? (
                    <div id="ccbottom">
                        <TTTControl play={ play } id={ props.id } changeShape={ props.changeShape} changeOppShape={ props.changeOppShape } changePlayer={ props.changePlayer } oppShape={ props.oppShape } />
                    </div>
                ) : <div id="ccbottom"></div>}

                <div id='mobilemodebar'>
                    <div id='mmbleft' className={"mmboption " + (play === 'online' ? 'active' : '')} onClick={() => setPlay('online')}>
                        Online
                    </div>
                    <div id='mmbright' className={"mmboption " + (play === 'vsfriend' ? 'active' : '')} onClick={() => setPlay('vsfriend')}>
                        Vs Friend
                    </div>
                </div>
            </div>
        );    
    }
    else {
        return (
            <div id="tttwait">
                <div id='twcont'>
                    <div id='twleft'>
                        <div id="drawcont">
                            <div id="drawshowcase">
                                <label>Draw your own shape</label>
                            </div>
                            <div id='drawplace'>
                                <DrawSpot handleCustomShapeChange={ handleCustomShapeChange } handleShapeChange={ handleCustomShapeChange } selected={(props.shape === x || props.shape === o ? false : true)} player={( props.firstShape === x ? 1 : 2)} />
                            </div>
                        </div>
                    </div>
                    <div id='twright'>

                    </div>
                </div>
            </div>
        )
    }
}
 
export default TTTConnect;


