import React, { Component, useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

import './Navbar.css';

import { isMobile } from 'react-device-detect';

import burger from '../../assets/burger.svg';
import burgerTilt from '../../assets/burger-tilt.svg';

const Navbar = (props) => {

    // const forceUpdate = () => {
    //     const [value, setValue] = useState(0); // integer state
    //     return () => setValue(value => value + 1); // update the state to force render
    // }
    // useEffect(() => {
    //     props.navigation.addListener(
    //         'didFocus',
    //         payload => {
    //             forceUpdate();
    //         }
    //     );
    // }, [])

    const [windowWidth, setWindowWidth] = useState(window.innerWidth);
    const [windowHeight, setWindowHeight] = useState(window.innerHeight);
    const [windowScroll, setWindowScroll] = useState(window.scrollY);
    
    const handleResize = () => {
        setWindowWidth(window.innerWidth);
        setWindowHeight(window.innerHeight);
    }
    window.addEventListener('resize', handleResize);

    const [url, setUrl] = useState(window.location.pathname);

    var logged = false;

    let menu = [
        {
            id: 1,
            title: 'Home',
            to: '/H',
        },
        {
            id: 2,
            title: 'Games',
            to: '/G',
        }
    ]

    var linkId;
    switch(url.split('/')[1]) {
        case '':
            break;
        case 'H':
            linkId = 1;
            break
        case 'G':
            linkId = 2;
            break
        case 'Chess':
            linkId = 3;
            menu.push({
                id: 3,
                title: 'Chess',
                to: '/Chess',
            });
            break
        case 'Sudoku':
            linkId = 3;
            menu.push({
                id: 3,
                title: 'Sudoku',
                to: '/Sudoku',
            });
            break
        case 'TTT':
            linkId = 3;
            menu.push({
                id: 3,
                title: 'Tic Tac Toe',
                to: '/TTT',
            });
            break
        case 'Memo':
            linkId = 3;
            menu.push({
                id: 3,
                title: 'Memo',
                to: '/Memo',
            });
            break
        default:
            linkId = 3;
            menu.push({
                id: 3,
                title: '404',
                to: '/H',
            });
            break
    }

    const [activeLink, setActiveLink] = useState(linkId);

    const handleClick = (id) => {
        setActiveLink(id);
    }

    return (
        <div id='navbar' className={ windowWidth < window.screen.width - windowHeight - 160 + 50 + ((windowHeight - 160) / 1.875) + 50 || (windowWidth < 1100 && !isMobile) ? 'mid ' : '' + (isMobile ? 'mobile ' : '')}>
            <div id='nbcont'>
                <div id='nblogo'>
                    <a href='/' id='nbl'>
                        GaymeHub
                    </a>
                    <div id='nbburger'>
                        <img src={ burger } alt="" />
                    </div>
                </div>
                <div id='nbmenu'>
                    <div id='nbmenucont'>

                        {menu.map((option) => {
                            if (option.id === 1 && logged) {
                                return (
                                    <Link key={ option.id } className={'nbmenuoption ' + (activeLink === option.id ? 'active ' : '')} to={ option.to } onClick={() => handleClick(option.id)}>
                                        <label htmlFor="">{ option.title }</label>
                                    </Link>
                                )
                            }
                            else if (option.id !== 1) {
                                return (
                                    <Link key={ option.id } className={'nbmenuoption ' + (activeLink === option.id ? 'active ' : '')} to={ option.to } onClick={() => handleClick(option.id)}>
                                        <label htmlFor="">{ option.title }</label>
                                    </Link>
                                )
                            }
                        })}

                        {/* <div className='nbmenuoption'>
                            <label htmlFor="">Games</label>
                        </div> */}
                    </div>
                </div>
                <div id='nbaccount'>
                    <button id='loginbtn'><label htmlFor="">Log in</label></button>
                    <button id='joinbtn'>Join</button>
                </div>
            </div>
        </div>
    );
}
 
export default Navbar;



