import Piece from "./Piece";

import Pawn from './Pawn';

import kingWhite from '../../../../assets/chess/king-white.svg'
import kingBlack from '../../../../assets/chess/king-black.svg'
import Rook from "./Rook";

class King extends Piece {
    constructor(player) {
        super(player, (player === 1 ? kingWhite : kingBlack));
        this.initialPos = {
            1: [7, 4],
            2: [7, 3]
        }
        this.isHit = false;
        this.moved = false;
    }

    getPossibleMoves(startI, squares) {

        let possiblePos = []

        const row = startI[0];
        const col = startI[1];

        let checkedPos = [];
        for (let i = 0; i < squares.length; i++) {
            for(let j = 0; j < squares[i].length; j++) {
                if (squares[i][j]) {
                    if (squares[i][j].player !== this.player && !(squares[i][j] instanceof King)) {
                        if (squares[i][j] instanceof Pawn) {
                            checkedPos = checkedPos.concat(squares[i][j].getOppHitPossibleMoves([i, j], squares));
                        }
                        else {
                            checkedPos = checkedPos.concat(squares[i][j].getHitPossibleMoves([i, j], squares));
                        }
                    }
                    else if (squares[i][j].player !== this.player && squares[i][j] instanceof King) {

                        if (i - 1 >= 0) {
                            checkedPos.push(`${i - 1},${j}`);
                        }
                        if (j - 1 >= 0) {
                            checkedPos.push(`${i},${j - 1}`);
                        }
                        if (i + 1 <= 7) {
                            checkedPos.push(`${i + 1},${j}`);
                        }
                        if (j + 1 <= 7) {
                            checkedPos.push(`${i},${j + 1}`);
                        }
                        if (i - 1 >= 0 && j - 1 >= 0) {
                            checkedPos.push(`${i - 1},${j - 1}`);
                        }
                        if (i - 1 >= 0 && j + 1 <= 7) {
                            checkedPos.push(`${i - 1},${j + 1}`);
                        }
                        if (i + 1 <= 7 && j - 1 >= 0) {
                            checkedPos.push(`${i + 1},${j - 1}`);
                        }
                        if (i + 1 <= 7 && j + 1 <= 7) {
                            checkedPos.push(`${i + 1},${j + 1}`);
                        }
                
                    }
                }
            }
        }

        if (row - 1 >= 0) {
            if (!checkedPos.includes(`${row - 1},${col}`)) {
                if (squares[row - 1][col] !== null) {
                    if (squares[row - 1][col].player !== this.player) {
                        possiblePos.push(`${row - 1},${col}`);
                    }
                }
                else {
                    possiblePos.push(`${row - 1},${col}`);
                }
            }
        }
        if (col - 1 >= 0) {
            if (!checkedPos.includes(`${row},${col - 1}`)) {
                if (squares[row][col - 1] !== null) {
                    if (squares[row][col - 1].player !== this.player) {
                        possiblePos.push(`${row},${col - 1}`);
                    }
                }
                else {
                    possiblePos.push(`${row},${col - 1}`);
                }
            }        
        }
        if (row + 1 <= 7) {
            if (!checkedPos.includes(`${row + 1},${col}`)) {
                if (squares[row + 1][col] !== null) {
                    if (squares[row + 1][col].player !== this.player) {
                        possiblePos.push(`${row + 1},${col}`);
                    }
                }
                else {
                    possiblePos.push(`${row + 1},${col}`);
                }
            }
        }
        if (col + 1 <= 7) {
            if (!checkedPos.includes(`${row},${col + 1}`)) {
                if (squares[row][col + 1] !== null) {
                    if (squares[row][col + 1].player !== this.player) {
                        possiblePos.push(`${row},${col + 1}`);
                    }
                }
                else {
                    possiblePos.push(`${row},${col + 1}`);
                }
            }        
        }
        if (row - 1 >= 0 && col - 1 >= 0) {
            if (!checkedPos.includes(`${row - 1},${col - 1}`)) {
                if (squares[row - 1][col - 1] !== null) {
                    if (squares[row - 1][col - 1].player !== this.player) {
                        possiblePos.push(`${row - 1},${col - 1}`);
                    }
                }
                else {
                    possiblePos.push(`${row - 1},${col - 1}`);
                }
            }        
        }
        if (row - 1 >= 0 && col + 1 <= 7) {
            if (!checkedPos.includes(`${row - 1},${col + 1}`)) {
                if (squares[row - 1][col + 1] !== null) {
                    if (squares[row - 1][col + 1].player !== this.player) {
                        possiblePos.push(`${row - 1},${col + 1}`);
                    }
                }
                else {
                    possiblePos.push(`${row - 1},${col + 1}`);
                }
            }        
        }
        if (row + 1 <= 7 && col - 1 >= 0) {
            if (!checkedPos.includes(`${row + 1},${col - 1}`)) {
                if (squares[row + 1][col - 1] !== null) {
                    if (squares[row + 1][col - 1].player !== this.player) {
                        possiblePos.push(`${row + 1},${col - 1}`);
                    }
                }
                else {
                    possiblePos.push(`${row + 1},${col - 1}`);
                }
            }        
        }
        if (row + 1 <= 7 && col + 1 <= 7) {
            if (!checkedPos.includes(`${row + 1},${col + 1}`)) {
                if (squares[row + 1][col + 1] !== null) {
                    if (squares[row + 1][col + 1].player !== this.player) {
                        possiblePos.push(`${row + 1},${col + 1}`);
                    }
                }
                else {
                    possiblePos.push(`${row + 1},${col + 1}`);
                }
            }        
        }


        if (!this.moved) {

            let flag = false;

            if (squares[7][7] instanceof Rook) {
                for (let i = col + 1; i < 7; i++) {
                    if (squares[row][i] !== null) {
                        flag = true;
                    }
                }

                if (!flag) {
                    possiblePos.push(`${row},${col + 2}`);
                }
            }

            flag = false;
            if (squares[7][0] instanceof Rook) {
                for (let i = col - 1; i > 0; i--) {
                    if (squares[row][i] !== null) {
                        flag = true;
                    }
                }

                if (!flag) {
                    possiblePos.push(`${row},${col - 2}`);
                }
            }

        }

        return possiblePos;

    }

    getHitPossibleMoves() {
        return [];
    }


}


export default King;
