import React, { Component, useState, useEffect, useContext } from 'react';

import './TTTControl.css';

import arrowDownFat from '../../../../assets/small-arrow-down-fat.svg';
import arrowDown from '../../../../assets/small-arrow-down.svg';

import x from '../../../../assets/ttt/x.svg';
import o from '../../../../assets/ttt/o.svg';

import { SocketContext } from '../../../../context/socketTTT';
import DrawSpot from './DrawSpot/DrawSpot';
import { isMobile } from 'react-device-detect';

const TTTControl = (props) => {

    const [open, setOpen] = useState(false);

    const [shape, setShape] = useState(x);
    const [player, setPlayer] = useState(1);

    const [ready, setReady] = useState(false);

    const [search, setSearch] = useState(false);

    const socket = useContext(SocketContext);

    // useEffect(() => {

    //     if (!props.id) {
    //         props.changeShape(shape);
    //         props.changeOppShape((shape === x ? o : x));

    //         props.changePlayer(player);
    //     }

    //     return () => {
            
    //     }
    // }, []);

    useEffect(() => {

        if (!props.id) {
            props.changeShape(shape);
            props.changeOppShape((shape === x ? o : x));

            props.changePlayer(player);
        }

        socket.on('joined', () => {
            setReady(true);

            socket.emit('changeShape', (shape === x || shape === o ? (shape === x ? o : x) : (player === 1 ? o : x)));
            socket.emit('changeOppShape', (shape));

            socket.emit('changePlayer', (player === 1 ? 2 : 1));
        });

        return () => {
            
        }
    }, [shape, player])

    const handleOpenClick = () => {
        setOpen(!open);
    }


    const handleCustomShapeChange = (shape) => {
        setShape(shape);
        props.changeShape(shape);
        
        // socket.emit('changeShape', x);
        socket.emit('changeOppShape', shape);
    }

    const handleShapeClick = (shape) => {
        setShape(shape);
        props.changeShape(shape);

        setPlayer((shape === x ? 1 : 2));
        props.changePlayer((shape === x ? 1 : 2))

        if (ready) {
            if (props.oppShape === x || props.oppShape === o) {
                socket.emit('changeShape', (shape === x ? o : x));
            }
            socket.emit('changeOppShape', shape);
            socket.emit('changePlayer', (shape === x ? 2 : 1));
        }
    }

    const handlePlayerClick = (player) => {
        setPlayer(player);
        props.changePlayer(player);

        socket.emit('changePlayer', (player === 1 ? 2 : 1))
    }

    // const handleShapeChange = (shape) => {
    //     setShape(shape);
    //     props.changeShape(shape);

    //     socket.emit('changeShape', (shape === x ? o : x));
    // }

    // const handleShapeClick = (prop) => {
    //     if (prop === x || prop === o) {
    //         setShape(shape);
    //         props.changeShape(shape);
    
    //         setPlayer((prop === x ? 1 : 2));
    //         props.changePlayer((prop === x ? 1 : 2))

    //         if (ready) {
    //             socket.emit('changeShape', (prop === x ? o : x));
    //             socket.emit('changePlayer', (prop === x ? 2 : 1));
    //         }
    //     }
    //     else {
    //         setPlayer(prop);
    //         props.changePlayer(prop);
    //         if (ready) {
    //             socket.emit('changePlayer', (prop === 1 ? 2 : 1));
    //         }
    //     }
    // }

    const handleStartGameClick = () => {
        if (props.play === 'vsfriend') {
            if (ready) {
                socket.emit('startGame');
            }
        }
        else if (props.play === 'online') {
            if (search) {
                socket.emit('cancelSearch');
                setSearch(false);
            }
            else {
                socket.emit('playOnline', shape);
                setSearch(true);
                // setOpen(false);
            }
        }
    }

    return (
        // <div id="gamecontrolcont" style={(props.play === 'vsfriend' ? {marginRight: '100px'} : {})}>
        <div id="gamecontrolcont" style={(props.play === 'online' ? {marginRight: '0px'} : {})}>

            <div id="drawcont" style={(open ? {height: (isMobile ? '360px' : '320px')} : {})}>
                <div id="drawshowcase">
                    <label>{(isMobile ? 'Draw' : 'Draw your own shape')}</label>
                    <div id='dsdropdown' onClick={ handleOpenClick }>
                        <img src={ arrowDownFat } alt="" style={(open ? {transform: 'rotate(-180deg)'} : {})} />
                    </div>
                </div>
                <div id='drawplace'>
                    <DrawSpot handleCustomShapeChange={ handleCustomShapeChange } handleShapeChange={ handleShapeClick } selected={(shape === x || shape === o ? false : true)} player={ player } />
                </div>
            </div>
            <div id="colorcont" style={(props.play === 'online' ? {display: 'none'} : {})}>
                <div id="whitebtn" className={"colorbtn " + (shape === x || player === 1 ? 'active' : '')} onClick={() => (shape === x || shape === o ? handleShapeClick(x) : handlePlayerClick(1))}>
                    {(shape === x || shape === o ? <img src={ x } alt="" /> : 'Player 1')}
                </div>
                <div id="blackbtn" className={"colorbtn " + (shape === o || player === 2 ? 'active' : '')} onClick={() => (shape === x || shape === o ? handleShapeClick(o) : handlePlayerClick(2))}>
                {(shape === x || shape === o ? <img src={ o } alt="" /> : 'Player 2')}
                </div>
            </div>
            <button id="playbutton" className={(ready ? 'ready ' : '') + (props.play === 'online' ? 'ready ' : '')} style={(props.play === 'online' ? {marginTop: '20px'} : {})} onClick={ handleStartGameClick }>{(props.play === 'online' ? (!search ? 'Start search' : 'Cancel search') : 'Start game')}</button>

        </div>
    );
}
 
export default TTTControl;




