import React, { Component, useState, useContext, useEffect } from 'react';

import { SocketContext } from '../../../../context/socketTTT';

import './TTTInvite.css';


const TTTInvite = () => {

    const socket = useContext(SocketContext);

    const [room, setRoom] = useState(null);

    const [joined, setJoined] = useState(false);

    useEffect(() => {
        
        socket.on('roomNum', (room) => {
            setRoom(room);
        });

        socket.on('joined', () => {
            setJoined(true);
        });

        socket.on('left', () => {
            setJoined(false);
        });

        return () => {
            
        }
    }, [])

    const handleClick = () => {
        if (!room) {
            socket.emit('create');
        }
        else {
            const elem = document.createElement('textarea');
            elem.value = window.location.origin + '/TTT/' + room;
            document.body.appendChild(elem);
            elem.select();
            document.execCommand('copy');
            document.body.removeChild(elem);
        }
    }

    return (
        <div id="tttinvitecont" style={(room ? {height: '440px'} : {})}>

            <div className="cioption">
                <div className="ciheadercont">
                    <label>Invite friend</label>
                </div>
                <div id="cisearchcont">
                    <input type="text" placeholder="Username or Email" />
                </div>
            </div>

            <div className="cioption">
                <div className="ciheadercont">
                    <label>Invite friend with link</label>
                </div>
                <button id="getlinkbtn" onClick={ handleClick } className={(room ? 'room' : '' )}>{(room ? window.location.origin + 'TTT/' + room : '> Get link <' )}</button>
            </div>

            
            <label id="waiting">{(!joined ? 'Wait for opponent to join' : 'Opponent has joined')}</label>

        </div>
    );
}
 
export default TTTInvite;


