import React, { Component, useState } from 'react';

import ScrollContainer from 'react-indiana-drag-scroll';

import './GameCategory.css';

import arrowDown from '../../assets/arrow-down.svg';

const GameCategory = (option) => {

    const props = option.props;

    const [open, setOpen] = useState(true);

    const handleClick = () => {
        if (option.opnbl) {
            setOpen(!open);
        }
    }

    return (
        <div key={ props.id } className="gamesoption">
            <div className="goheader" onClick={ handleClick }>
                <label>{ props.title }</label>
                {option.opnbl && (
                    <img src={ arrowDown } alt="" style={(open ? {transform: 'rotate(-180deg)'} : {})} />
                )}
            </div>
            <ScrollContainer vertical={ false } className="gogamescont" style={(!open ? {height: '0px', paddingTop: '0px'} : {})}>
                {props.games.map((game) => {
                    return (
                        <a key={ game.id } href={'/' + game.title} className="gogame">
                            <div><img src={ game.img } alt="" /></div>
                        </a>
                    )
                })}
            </ScrollContainer>
        </div>
    );
}
 
export default GameCategory;

