import React, { Component, useState, useRef } from 'react';

import './Sudoku.css';

import undo from '../../assets/sudoku/undo.svg';
import erase from '../../assets/sudoku/erase.svg';
import hint from '../../assets/sudoku/hint.svg';

import { main, fillGrid, difficulty, getSolvedGrid } from './alg/main';
import { grid, repr } from './alg/grids';

import { isMobile } from 'react-device-detect';

import Grid from './Game/Grid';

const rend = (arr) => {
    fillGrid(arr);
    return main(arr, 32);
}


const fSolvedGrid = getSolvedGrid(grid);
const fPlayGrid = main(fSolvedGrid, 53);

const Sudoku = () => {

    const gridRef = useRef()

    const [count1, setCount1] = useState(9);
    const [count2, setCount2] = useState(9);
    const [count3, setCount3] = useState(9);
    const [count4, setCount4] = useState(9);
    const [count5, setCount5] = useState(9);
    const [count6, setCount6] = useState(9);
    const [count7, setCount7] = useState(9);
    const [count8, setCount8] = useState(9);
    const [count9, setCount9] = useState(9);

    const changeCount1 = (count) => {
        setCount1(count);
    }
    const changeCount2 = (count) => {
        setCount2(count);
    }
    const changeCount3 = (count) => {
        setCount3(count);
    }
    const changeCount4 = (count) => {
        setCount4(count);
    }
    const changeCount5 = (count) => {
        setCount5(count);
    }
    const changeCount6 = (count) => {
        setCount6(count);
    }
    const changeCount7 = (count) => {
        setCount7(count);
    }
    const changeCount8 = (count) => {
        setCount8(count);
    }
    const changeCount9 = (count) => {
        setCount9(count);
    }

    const [windowWidth, setWindowWidth] = useState(window.innerWidth);
    const [windowHeight, setWindowHeight] = useState(window.innerHeight);
    const [windowScroll, setWindowScroll] = useState(window.scrollY);
    
    const handleResize = () => {
        setWindowWidth(window.innerWidth);
        setWindowHeight(window.innerHeight);
    }
    window.addEventListener('resize', handleResize)

    const handleScroll = () => {
        setWindowScroll(window.scrollY);
    }
    window.addEventListener('scroll', handleScroll);

    const [open, setOpen] = useState(false);

    const [diff, setDiff] = useState('Medium');
    const diffs = {
        62: 'Easy',
        53: 'Meduim',
        44: 'Hard',
        35: 'Very hard',
        26: 'Insane',
        17: 'Impossible',
    }
    
    const handleNumClick = (num) => {
        gridRef.current.changeCell(num);
    }

    const handleEraseClick = () => {
        gridRef.current.eraseCell();
    }

    const handleHintClick = () => {
        gridRef.current.hintCell();
    }

    // const solvedGrid = getSolvedGrid(grid);
    // const playGrid = main(solvedGrid, 62);

    const [solvedGrid, setSolvedGrid] = useState(fSolvedGrid);
    const [playGrid, setPlayGrid] = useState(fPlayGrid);

    const handleGameClick = (difficulty) => {
        const ssolvedGrid = getSolvedGrid(grid);
        const pplayGrid = main(ssolvedGrid, difficulty);

        gridRef.current.updateGrid(ssolvedGrid, pplayGrid);
       
        window.scrollTo(0, 0);

        setSolvedGrid(ssolvedGrid);
        setPlayGrid(pplayGrid);

        setDiff(diffs[difficulty]);
        setOpen(false);
    }

    const handleRestartClick = () => {
        gridRef.current.updateGrid(solvedGrid, playGrid);
        setOpen(false);
    }

    const handleUndoClick = () => {
        gridRef.current.undoGrid();
    }

    const handleCloseClick = () => {
        setOpen(false);
    }


    return (
        // 100vh - 80px - 2 * 60px - 10px
        // 100vh - 80px - 2 * 60px - 30px - 2 * ((100vh - 80px - 2 * 60px - 30px) / 6.75)
        <div id="sudoku" className={(windowWidth < windowHeight - 210 + windowHeight - 230 - 2 * ((windowHeight - 230) / 6.75) + 20 + 100 || (windowWidth < 1020 && !isMobile) ? 'mid ' : '') + (isMobile ? 'mobile ' : '')}>
            {/* { windowHeight - 210 + windowHeight - 230 - 2 * ((windowHeight - 230) / 6.75) + 120 } */}
            <Grid grid={playGrid} solvedGrid={solvedGrid} ref={gridRef} diff={ diff } changeGame={ handleGameClick } handleCloseClick={ handleCloseClick } handleNumClick={ handleNumClick } changeCount1={ changeCount1 } changeCount2={ changeCount2 } changeCount3={ changeCount3 } changeCount4={ changeCount4 } changeCount5={ changeCount5 } changeCount6={ changeCount6 } changeCount7={ changeCount7 } changeCount8={ changeCount8 } changeCount9={ changeCount9 }/>
            
            <div id="control">
                <div id='newgamecont'>
                    {/* {height: ((window.innerHeight - 230) / 6.75) / 0.2 + 'px'} */}
                    <div id='newgame' style={(open ? {height: ((window.innerHeight - 80 - 2 * 60 - 30) / 6.75) / 0.2 + (window.innerHeight - 80 - 2 * 60 - 30) / 6.75 + 'px'} : {})}>
                        <button id="newgamebtn" onClick={() => {setOpen(!open)}}>{(!open ? 'New game' : 'Close')}</button>
                        <div id='newgamemenu'>
                            <div className='menuoption' onClick={() => handleGameClick(62)}>
                                Easy
                            </div>
                            <div className='menuoption' onClick={() => handleGameClick(53)}>
                                Medium
                            </div>
                            <div className='menuoption' onClick={() => handleGameClick(44)}>
                                Hard
                            </div>
                            <div className='menuoption' onClick={() => handleGameClick(35)}>
                                Very hard
                            </div>
                            <div className='menuoption' onClick={() => handleGameClick(26)}>
                                Insane
                            </div>
                            <div className='menuoption' onClick={ handleRestartClick }>
                                Restart
                            </div>
                        </div>
                    </div>
                </div>
                <div id="controlbtncont">
                    <div className='cbtn' onClick={ handleUndoClick }>
                        <img src={ undo } alt="" style={{height: '22px'}} />
                        <label htmlFor="">Undo</label>
                    </div>
                    <div className='cbtn' onClick={ handleEraseClick }>
                        <img src={ erase } alt="" />
                        <label htmlFor="">Erase</label>
                    </div>
                    <div className='cbtn' onClick={ handleHintClick }>
                        <img src={ hint } alt="" />
                        <label htmlFor="">Hint</label>
                    </div>
                </div>
                {/* <div id="solvebtn">
                    SOLVE
                </div> */}
                <div id='btncont'>
                    <div className='btn' onClick={() => handleNumClick(1)}>1<div>{(count1 > 0 ? count1 : '')}</div></div>
                    <div className='btn' onClick={() => handleNumClick(2)}>2<div>{(count2 > 0 ? count2 : '')}</div></div>
                    <div className='btn' onClick={() => handleNumClick(3)}>3<div>{(count3 > 0 ? count3 : '')}</div></div>
                    <div className='btn' onClick={() => handleNumClick(4)}>4<div>{(count4 > 0 ? count4 : '')}</div></div>
                    <div className='btn' onClick={() => handleNumClick(5)}>5<div>{(count5 > 0 ? count5 : '')}</div></div>
                    <div className='btn' onClick={() => handleNumClick(6)}>6<div>{(count6 > 0 ? count6 : '')}</div></div>
                    <div className='btn' onClick={() => handleNumClick(7)}>7<div>{(count7 > 0 ? count7 : '')}</div></div>
                    <div className='btn' onClick={() => handleNumClick(8)}>8<div>{(count8 > 0 ? count8 : '')}</div></div>
                    <div className='btn' onClick={() => handleNumClick(9)}>9<div>{(count9 > 0 ? count9 : '')}</div></div>
                    <div id='erasebtn' className='btn' onClick={ handleEraseClick }>
                        <img src={ erase } alt="" />
                    </div>
                </div>

                <div id="controlbtncontbottom">
                    <div className='cbtn' onClick={ handleUndoClick }>
                        <img src={ undo } alt="" style={{height: '20px'}} />
                        <label htmlFor="">Undo</label>
                    </div>
                    <div className='cbtn' onClick={ handleHintClick }>
                        <img src={ hint } alt="" />
                        <label htmlFor="">Hint</label>
                    </div>
                </div>


                <div id='newgamemenubottom'>
                    <div className='menuoption' onClick={() => handleGameClick(62)}>
                        Easy
                    </div>
                    <div className='menuoption' onClick={() => handleGameClick(53)}>
                        Medium
                    </div>
                    <div className='menuoption' onClick={() => handleGameClick(44)}>
                        Hard
                    </div>
                    <div className='menuoption' onClick={() => handleGameClick(35)}>
                        Very hard
                    </div>
                    <div className='menuoption' onClick={() => handleGameClick(26)}>
                        Insane
                    </div>
                    <div className='menuoption' onClick={ handleRestartClick }>
                        Restart
                    </div>
                </div>



            </div>
        </div>
    );
}
 
export default Sudoku;

