import React, { Component } from 'react';

import { SocketContext, socket } from '../../../context/socketChess';

import './ChessPlay.css';

import { isMobile } from 'react-device-detect';

import ChessBoard from './ChessBoard/ChessBoard';

import Pawn from './Pieces/Pawn';
import King from './Pieces/King';
import Queen from './Pieces/Queen';
import Bishop from './Pieces/Bishop';
import Knight from './Pieces/Knight';
import Rook from './Pieces/Rook';

import queenWhiteImg from '../../../assets/chess/queen-white.svg'
import queenBlackImg from '../../../assets/chess/queen-black.svg'

import rookWhiteImg from '../../../assets/chess/rook-white.svg'
import rookBlackImg from '../../../assets/chess/rook-black.svg'

import knightWhiteImg from '../../../assets/chess/knight-white.svg'
import knightBlackImg from '../../../assets/chess/knight-black.svg'

import bishopWhiteImg from '../../../assets/chess/bishop-white.svg'
import bishopBlackImg from '../../../assets/chess/bishop-black.svg'

import settings from '../../../assets/settings.svg';

import lpmainGrey from '../../../assets/lpmain-grey.svg';
import lpmainDarkGrey from '../../../assets/lpmain-darkgrey.svg';

const initialiseChessBoard = (player) => {

    var squares;

    if (player === 1) {
        squares = [
            [new Rook(2), new Knight(2), new Bishop(2), new Queen(2), new King(2), new Bishop(2), new Knight(2), new Rook(2)],
            [new Pawn(2), new Pawn(2), new Pawn(2), new Pawn(2), new Pawn(2), new Pawn(2), new Pawn(2), new Pawn(2)],
            [null, null, null, null, null, null, null, null],
            [null, null, null, null, null, null, null, null],
            [null, null, null, null, null, null, null, null],
            [null, null, null, null, null, null, null, null],
            [new Pawn(1), new Pawn(1), new Pawn(1), new Pawn(1), new Pawn(1), new Pawn(1), new Pawn(1), new Pawn(1)],
            [new Rook(1), new Knight(1), new Bishop(1), new Queen(1), new King(1), new Bishop(1), new Knight(1), new Rook(1)],
        ]
    }
    else if (player === 2) {
        squares = [
            [new Rook(1), new Knight(1), new Bishop(1), new King(1), new Queen(1), new Bishop(1), new Knight(1), new Rook(1)],
            [new Pawn(1), new Pawn(1), new Pawn(1), new Pawn(1), new Pawn(1), new Pawn(1), new Pawn(1), new Pawn(1)],
            [null, null, null, null, null, null, null, null],
            [null, null, null, null, null, null, null, null],
            [null, null, null, null, null, null, null, null],
            [null, null, null, null, null, null, null, null],
            [new Pawn(2), new Pawn(2), new Pawn(2), new Pawn(2), new Pawn(2), new Pawn(2), new Pawn(2), new Pawn(2)],
            [new Rook(2), new Knight(2), new Bishop(2), new King(2), new Queen(2), new Bishop(2), new Knight(2), new Rook(2)],
        ]
    }

    return squares;
}

class ChessPlay extends React.Component {
    
    static socket = socket;

    constructor(props) {
        super(props);
        this.state = {
            squares: initialiseChessBoard(this.props.player),
            whiteFallen: [],
            blackFallen: [],

            player: this.props.player,
            turn: 1,
            
            timeYouMin: (this.props.mode).split('/')[0],
            timeYouSec: (this.props.mode).split('/')[1],

            timeOppMin: (this.props.mode).split('/')[0],
            timeOppSec: (this.props.mode).split('/')[1],

            checked: false,
            checkPiecePos: null,

            activePiece: null,
            activePiecePos: null,

            activePieceHover: null,

            // kingYou: (this.props.player === 1 ? [7, 4] : [7, 3]),
            // kingOpponent: (this.props.player === 1 ? [0, 4] : [0, 3]),

            result: null,

            draw: false,
            drawRequest: false,

            pawn: false,
            pawnPos: null,

            moves: [],

            playAgain: 'No',

            // boardSize: window.innerHeight - 160,
            // boardSize: Math.min(this.props.windowHeight - 160, this.props.windowHeight - 100),
            boardSize: (isMobile ? window.innerWidth - 40 : Math.min(window.innerHeight - 160, window.innerWidth - 100)),

            gay: false,
        }

        this.cord = {0: 'a', 1: 'b', 2: 'c', 3: 'd', 4: 'e', 5: 'f', 6: 'g', 7: 'h'};

        this.kingYou = (this.props.player === 1 ? [7, 4] : [7, 3]);
        this.kingOpponent = (this.props.player === 1 ? [0, 4] : [0, 3]);

        const handleResize = () => {
            this.setState({boardSize: (isMobile ? window.innerWidth - 40 : Math.min(window.innerHeight - 160, window.innerWidth - 100))});
        }
        window.addEventListener('resize', handleResize);
    

        socket.on('updateTime', (min, sec) => {
            this.setState({timeOppMin: min, timeOppSec: sec});
        });

        socket.on('move', (start, end, move) => {
            var squares = [];
            for (let i = 0; i < this.state.squares.length; i++)
                squares[i] = this.state.squares[i].slice();
            
            squares[end[0]][end[1]] = squares[start[0]][start[1]];
            squares[start[0]][start[1]] = null;

            let tempMoves = this.state.moves.slice();
            if (move) {
                tempMoves.push(move);
            }

            // this.setState({squares: squares, moves: tempMoves});  
            
            let draw = true;
            for (let i = 0; i < squares.length; i++) {
                for (let j = 0; j < squares[i].length; j++) {
                    if (squares[i][j] && squares[i][j].player === this.state.player) {
                        if (squares[i][j].getPossibleMoves([i, j], squares).length > 0){ 
                            draw = false;
                        }
                    }
                }
            }

            let check = false;
            let pos = [];

            let checkedPos = [];
            for (let i = 0; i < squares.length; i++) {
                for(let j = 0; j < squares[i].length; j++) {
                    if (squares[i][j]) {
                        if (squares[i][j].player !== this.state.player && !(squares[i][j] instanceof King)) {
                            if (squares[i][j] instanceof Pawn) {
                                checkedPos = checkedPos.concat(squares[i][j].getOppHitPossibleMoves([i, j], squares));
                                if (checkedPos.includes(`${this.kingYou[0]},${this.kingYou[1]}`)) {
                                    check = true;
                                    pos = [i, j]
                                }
                            }
                            else {
                                checkedPos = checkedPos.concat(squares[i][j].getPossibleMoves([i, j], squares));
                                if (checkedPos.includes(`${this.kingYou[0]},${this.kingYou[1]}`)) {
                                    check = true;
                                    pos = [i, j]
                                }
                            }
                        }
                    }
                }
            }


            if (draw) {
                this.setState({squares: squares, moves: tempMoves, result: 'draw'});  
                socket.emit('end', 'draw');
            }
            else {
                this.setState({squares: squares, moves: tempMoves, checked: check, checkPiecePos: pos});
            }
        });

        socket.on('pawnChange', (pos, piece) => {
            var squares = [];
            for (var i = 0; i < this.state.squares.length; i++)
                squares[i] = this.state.squares[i].slice();

            const revPlayer = (this.state.player === 1 ? 2 : 1);
            squares[pos[0]][pos[1]] = (piece === 'queen' ? new Queen(revPlayer) : piece === 'rook' ? new Rook(revPlayer) : piece === 'knight' ? new Knight(revPlayer) : piece === 'bishop' ? new Bishop(revPlayer) : null);
            
            this.setState({squares: squares});
        });

        socket.on('changeTurn', (turn) => {
            this.setState({turn: turn});
        });

        socket.on('check', (pos) => {
            setTimeout(() => {
                this.setState({checked: true, checkPiecePos: pos});
            }, 100);
        });

        socket.on('kingMove', (pos) => {
            this.kingOpponent = pos;
        });

        socket.on('end', (res) => {
            this.setState({result: res, draw: false, drawRequest: false});
            clearInterval(this.interval);
        });

        socket.on('drawRequest', () => {
            this.setState({draw: true});
        });
        socket.on('declineDraw', () => {
            this.setState({drawRequest: false});
        });

        socket.on('playAgain', () => {
            this.setState({playAgain: 'Yes'})
        })
        socket.on('reset', () => {
            this.setState({
                squares: initialiseChessBoard(this.props.player),
                whiteFallen: [],
                blackFallen: [],
    
                player: this.props.player,
                turn: 1,
                
                timeYouMin: (this.props.mode).split('/')[0],
                timeYouSec: (this.props.mode).split('/')[1],
    
                timeOppMin: (this.props.mode).split('/')[0],
                timeOppSec: (this.props.mode).split('/')[1],
    
                checked: false,
                checkPiecePos: null,
    
                activePiece: null,
                activePiecePos: null,
    
                activePieceHover: null,
    
                kingYou: (this.props.player === 1 ? [7, 4] : [7, 3]),
                kingOpponent: (this.props.player === 1 ? [0, 4] : [0, 3]),
    
                result: null,
    
                draw: false,
                drawRequest: false,
    
                pawn: false,
                pawnPos: null,
    
                moves: [],

                playAgain: 'No',
            });
        });

        socket.on('left', () => {
            if (!this.state.result) {
                this.setState({result: 'win'});
            }
        });

    }

    componentDidUpdate() {

        // console.log(this.state.squares);

        const squares = [];
        for (let i = 0; i < this.state.squares.length; i++)
            squares.push(this.state.squares[i].slice());

        if (this.state.checked && !this.state.result) {
            let row = this.kingYou[0];
            let col = this.kingYou[1];

            // let checkedPos = [];
            // for (let i = 0; i < squares.length; i++) {
            //     for(let j = 0; j < squares[i].length; j++) {
            //         if (squares[i][j]) {
            //             if (squares[i][j].player !== this.state.player && !(squares[i][j] instanceof King)) {
            //                 checkedPos = checkedPos.concat(squares[i][j].getPossibleMoves([i, j], squares));
            //             }
            //         }
            //     }
            // }

            // let checkedPos = [];
            // for (let i = 0; i < squares.length; i++) {
            //     for(let j = 0; j < squares[i].length; j++) {
            //         if (squares[i][j]) {
            //             if (squares[i][j].player !== this.state.player && !(squares[i][j] instanceof King)) {
            //                 if (squares[i][j] instanceof Pawn) {
            //                     checkedPos = checkedPos.concat(squares[i][j].getOppHitPossibleMoves([i, j], squares));
            //                 }
            //                 else {
            //                     checkedPos = checkedPos.concat(squares[i][j].getPossibleMoves([i, j], squares));
            //                 }
            //             }
            //         }
            //     }
            // }

            let checkedPos = [];
            let save = false;
            for (let i = 0; i < squares.length; i++) {
                if (save) {
                    break;
                }
                for(let j = 0; j < squares[i].length; j++) {
                    if (squares[i][j]) {
                        if (squares[i][j].player === this.state.player && !(squares[i][j] instanceof King)) {
                            let piecePos = squares[i][j].getPossibleMoves([i, j], squares);

                            let tempFull = [];
                            for (let k = 0; k < squares.length; k++)
                                tempFull.push(squares[k].slice());

                            for(let k = 0; k < piecePos.length; k++) {
                                let pp = piecePos[k].split(',');
                                tempFull[pp[0]][pp[1]] = new Pawn(this.state.player);
                            }

                            let checkedPos = tempFull[this.state.checkPiecePos[0]][this.state.checkPiecePos[1]].getPossibleMoves(this.state.checkPiecePos, tempFull);

                            if (!checkedPos.includes(`${row},${col}`)) {
                                save = true;
                                break;
                            }

                            // savePos = savePos.concat(squares[i][j].getPossibleMoves([i, j], squares));

                        }
                    }
                }
            }

            if (squares[row][col].getPossibleMoves([row, col], squares).length === 0 && !save) {
                this.setState({result: 'lose'});
                socket.emit('end', 'win');
            }
        }

        clearInterval(this.interval);
        if (this.state.result) {
            return;
        }

        if (this.state.player === this.state.turn) {
            this.interval = setInterval(() => {
                
                if (Number(this.state.timeYouMin) === 0 && Number(this.state.timeYouSec) === 0) {
                    this.setState({result: 'lose'});
                    socket.emit('end', 'win');
                    clearInterval(this.interval)
                }
                else if (Number(this.state.timeYouMin) >= 0 && Number(this.state.timeYouSec) > 0) {
                    let sec = (this.state.timeYouSec - 1 > 9 ? `${this.state.timeYouSec - 1}` : `0${this.state.timeYouSec - 1}`);
                    this.setState({timeYouSec: sec});
                    socket.emit('updateTime', this.state.timeYouMin, sec);
                }
                else {
                    let min = this.state.timeYouMin - 1;
                    let sec = 59;
                    this.setState({timeYouMin: min, timeYouSec: '59'});
                    socket.emit('updateTime', min, '59');
                }

            }, 1000);
        }
        else {
            clearInterval(this.interval);
            // this.interval = setInterval(() => {
            //     console.log('---------------')
            //     if (Number(this.state.timeOppMin) === 0 && Number(this.state.timeOppSec) === 0) {
            //         this.setState({result: 'lose'});
            //         socket.emit('end', 'win');
            //         clearInterval(this.interval)
            //     }
            //     else if (Number(this.state.timeOppMin) >= 0 && Number(this.state.timeOppSec) > 0) {
            //         let sec = (this.state.timeOppSec - 1 > 9 ? `${this.state.timeOppSec - 1}` : `0${this.state.timeOppSec - 1}`);
            //         this.setState({timeOppSec: sec});
            //         socket.emit('updateTime', this.state.timeOppMin, sec);
            //     }
            //     else {
            //         let min = this.state.timeOppMin - 1;
            //         let sec = 59;
            //         this.setState({timeOppMin: min, timeOppSec: '59'});
            //         socket.emit('updateTime', min, '59');
            //     }

            // }, 1000);
        }

    }

    componentDidMount() {
        this.setState({boardSize: (isMobile ? window.innerWidth - 40 : Math.min(window.innerHeight - 160, window.innerWidth - 100))})
        if (this.state.player === this.state.turn) {
            this.interval = setInterval(() => {
                
                if (Number(this.state.timeYouMin) === 0 && Number(this.state.timeYouSec) === 0) {
                    this.setState({result: 'lose'});
                    socket.emit('end', 'win');
                    clearInterval(this.interval)
                }
                else if (Number(this.state.timeYouMin) >= 0 && Number(this.state.timeYouSec) > 0) {
                    let sec = (this.state.timeYouSec - 1 > 9 ? `${this.state.timeYouSec - 1}` : `0${this.state.timeYouSec - 1}`);
                    this.setState({timeYouSec: sec});
                    socket.emit('updateTime', this.state.timeYouMin, sec);
                }
                else {
                    let min = this.state.timeYouMin - 1;
                    let sec = 59;
                    this.setState({timeYouMin: min, timeYouSec: '59'});
                    socket.emit('updateTime', min, '59');
                }

            }, 1000);
        }
        else {
            clearInterval(this.interval);
            // this.interval = setInterval(() => {
            //     console.log('---------------')
            //     if (Number(this.state.timeOppMin) === 0 && Number(this.state.timeOppSec) === 0) {
            //         this.setState({result: 'lose'});
            //         socket.emit('end', 'win');
            //         clearInterval(this.interval)
            //     }
            //     else if (Number(this.state.timeOppMin) >= 0 && Number(this.state.timeOppSec) > 0) {
            //         let sec = (this.state.timeOppSec - 1 > 9 ? `${this.state.timeOppSec - 1}` : `0${this.state.timeOppSec - 1}`);
            //         this.setState({timeOppSec: sec});
            //         socket.emit('updateTime', this.state.timeOppMin, sec);
            //     }
            //     else {
            //         let min = this.state.timeOppMin - 1;
            //         let sec = 59;
            //         this.setState({timeOppMin: min, timeOppSec: '59'});
            //         socket.emit('updateTime', min, '59');
            //     }

            // }, 1000);
        }
    }
    componentWillUnmount() {
        clearInterval(this.interval);
    }

    
    handleMouseDown(e) {
        const elem = e.target;

        if (elem.classList.contains('piece')) {

            const row = Math.floor((e.clientY - document.getElementById('chessboard').offsetTop + window.scrollY) / (this.state.boardSize / 8));
            const col = Math.floor((e.clientX - document.getElementById('chessboard').offsetLeft + window.scrollX) / (this.state.boardSize / 8));

            if (this.state.squares[row][col] && this.state.squares[row][col].player === this.state.player && !this.state.result) {
                const ind = [row, col];
                
                this.setState({activePiece: elem, activePiecePos: ind});

                const x = e.clientX + window.scrollX - ((this.state.boardSize / 8) / 2);
                const y = e.clientY + window.scrollY - ((this.state.boardSize / 8) / 2);

                elem.style.position = "absolute";
                elem.style.left = `${x}px`;
                elem.style.top = `${y}px`;
            }
        }    
    }
    handleMouseMove(e) {
        const elem = this.state.activePiece;

        if (elem) {

            const x = e.clientX + window.scrollX - ((this.state.boardSize / 8) / 2);
            const y = e.clientY + window.scrollY - ((this.state.boardSize / 8) / 2);

            const row = Math.floor((e.clientY + window.scrollY - document.getElementById('chessboard').offsetTop) / (this.state.boardSize / 8));
            const col = Math.floor((e.clientX + window.scrollX - document.getElementById('chessboard').offsetLeft) / (this.state.boardSize / 8));

            this.setState({activePieceHover: [row, col]})

            // const boardLeft = document.getElementById('chessboard').offsetLeft - 15;
            // const boardRight = document.getElementById('chessboard').offsetLeft + this.state.boardSize - 60;

            // const boardTop = document.getElementById('chessboard').offsetTop - 15;
            // const boardBottom = this.state.boardSize + document.getElementById('chessboard').offsetTop - 70;

            const boardLeft = document.getElementById('chessboard').offsetLeft - (this.state.boardSize / 8 / 3);
            const boardRight = document.getElementById('chessboard').offsetLeft - 2 * (this.state.boardSize / 8 / 3) + document.getElementById('chessboard').offsetWidth;

            const boardTop = document.getElementById('chessboard').offsetTop - (this.state.boardSize / 8 / 3);
            const boardBottom = document.getElementById('chessboard').offsetTop + document.getElementById('chessboard').offsetWidth - 2 * (this.state.boardSize / 8 / 3);

            if (x > boardLeft && x < boardRight && y > boardTop && y < boardBottom) {
                elem.style.left = `${x}px`;
                elem.style.top = `${y}px`;
            }
            else {

            }

        }
    }
    handleMouseUp(e) {
        const elem = e.target;

        this.setState({activePieceHover: null});

        if (this.state.activePiece) {

            if (this.state.player === this.state.turn) {

                const row = Math.floor((e.clientY + window.scrollY - document.getElementById('chessboard').offsetTop) / (this.state.boardSize / 8));
                const col = Math.floor((e.clientX + window.scrollX - document.getElementById('chessboard').offsetLeft) / (this.state.boardSize / 8));
        
                const ind = [row, col];

                var squares = [];
                for (var i = 0; i < this.state.squares.length; i++)
                    squares[i] = this.state.squares[i].slice();

                let possibleMoves = squares[this.state.activePiecePos[0]][this.state.activePiecePos[1]].getPossibleMoves(this.state.activePiecePos, squares);


                if (ind !== this.state.activePiecePos && possibleMoves.includes(ind.toString())) {
                    
                    //////////////////////////////////// CHECKED AND KING ////////////////////////////////////

                    if (this.state.checked && squares[this.state.activePiecePos[0]][this.state.activePiecePos[1]] instanceof King) {
                        // console.log('checked and king')

                        if (Math.abs(col - this.state.activePiecePos[1]) === 1) {
                            squares[row][col] = squares[this.state.activePiecePos[0]][this.state.activePiecePos[1]];
                            squares[this.state.activePiecePos[0]][this.state.activePiecePos[1]] = null;
                            
                            squares[row][col].moved = true;

                            this.setState({squares: squares, checked: false, turn: (this.state.player === 1 ? 2 : 1)});
                            this.kingYou = ind;
                            socket.emit('kingMove', ind);

                            socket.emit('move', this.state.activePiecePos, ind);
                            // socket.emit('changeTurn', this.state.turn);
                        }
                        else {

                        }
                    }

                    //////////////////////////////////// NOT CHECKED AND SOMETHING ////////////////////////////////////

                    else if (!this.state.checked) {
                        // console.log('not checked and something')

                        let take = (squares[row][col] ? true : false);

                        squares[row][col] = squares[this.state.activePiecePos[0]][this.state.activePiecePos[1]];
                        squares[this.state.activePiecePos[0]][this.state.activePiecePos[1]] = null;
                        
                        let checkedPos = [];
                        for (let i = 0; i < squares.length; i++) {
                            for(let j = 0; j < squares[i].length; j++) {
                                if (squares[i][j]) {
                                    if (squares[i][j].player !== this.state.player && !(squares[i][j] instanceof King)) {
                                        if (squares[i][j] instanceof Pawn) {
                                            checkedPos = checkedPos.concat(squares[i][j].getOppHitPossibleMoves([i, j], squares));
                                        }
                                        else {
                                            checkedPos = checkedPos.concat(squares[i][j].getHitPossibleMoves([i, j], squares));
                                        }
                                    }
                                }
                            }
                        }
    
                        let guard = null;
                        var rookFrom = [];
                        var rookTo = [];

                        if ((squares[row][col] instanceof King ? !checkedPos.includes(`${row},${col}`) : !checkedPos.includes(this.kingYou.toString()))) {

                            if (squares[row][col] instanceof King) {
                                this.kingYou = [row, col]
                                socket.emit('kingMove', [row, col]);

                                squares[row][col].moved = true;
                                
                                if (Math.abs(col - this.state.activePiecePos[1]) > 1) {
                                    if (col > this.state.activePiecePos[1]) {
                                        squares[row][col - 1] = squares[row][7];
                                        squares[row][7] = null;

                                        rookFrom = [row, 7];
                                        rookTo = [row, col - 1];
                                    }
                                    else {
                                        squares[row][col + 1] = squares[row][0];
                                        squares[row][0] = null;

                                        rookFrom = [row, 0];
                                        rookTo = [row, col + 1];
                                    }
                                    guard = col - this.state.activePiecePos[1];
                                }
                            }

                            let check = false;
                            if (!(squares[row][col] instanceof King)) {
                                if (squares[ind[0]][ind[1]].getHitPossibleMoves([ind[0], ind[1]], squares).includes(this.kingOpponent.toString())) {
                                    // socket.emit('check', [row, col]);
                                    check = true;
                                }
                            }

                            let move = '';
                            if (guard) {
                                if (this.state.player === 1) {
                                    if (guard < 0) {
                                        move = 'o-o-o';
                                    }
                                    else {
                                        move = 'o-o';
                                    }
                                }
                                else {
                                    if (guard < 0) {
                                        move = 'o-o';
                                    }
                                    else {
                                        move = 'o-o-o';
                                    }
                                }
                            }
                            else {
                                // alert(squares[row][col].constructor.name)
                                if (!(squares[row][col] instanceof Pawn)) {
                                    if (squares[row][col] instanceof Knight) {
                                        move += 'N';
                                    }
                                    else {
                                        move += squares[row][col].constructor.name.charAt(0);
                                    }
                                }
                            
                                if (take) {
                                    move += (this.state.player === 1 ? this.cord[this.state.activePiecePos[1]] : this.cord[7 - this.state.activePiecePos[1]]);
                                    move += 'x';
                                    move += (this.state.player === 1 ? this.cord[col] : this.cord[7 - col]);
                                }
                                else {
                                    move += (this.state.player === 1 ? this.cord[col] : this.cord[7 - col]);
                                }

                                move += (this.state.player === 1 ? 8 - row : row + 1)
                                if (check) {
                                    move += '+'
                                }
                            }

                            if (squares[row][col] instanceof Pawn) {
                                if (row === 0) {
                                    this.setState({pawnPos: [row, col, move], pawn: true, squares: squares, turn: (this.state.player === 1 ? 2 : 1)});

                                    elem.style.position = "static";
        
                                    return;
                                }
                            }

                            // let check = false;
                            // if (!(squares[row][col] instanceof King)) {
                            //     if (squares[ind[0]][ind[1]].getHitPossibleMoves([ind[0], ind[1]], squares).includes(this.kingOpponent.toString())) {
                            //         // socket.emit('check', [row, col]);
                            //         check = true;
                            //     }
                            // }

                            // let move = (guard ? guard === 2 ? 'o-o' : guard === 3 ? 'o-o-o' : null : );
                            // let move = '';
                            // if (guard) {
                            //     if (this.state.player === 1) {
                            //         if (guard < 0) {
                            //             move = 'o-o-o';
                            //         }
                            //         else {
                            //             move = 'o-o';
                            //         }
                            //     }
                            //     else {
                            //         if (guard < 0) {
                            //             move = 'o-o';
                            //         }
                            //         else {
                            //             move = 'o-o-o';
                            //         }
                            //     }
                            // }
                            // else {
                            //     // alert(squares[row][col].constructor.name)
                            //     if (!(squares[row][col] instanceof Pawn)) {
                            //         if (squares[row][col] instanceof Knight) {
                            //             move += 'N';
                            //         }
                            //         else {
                            //             move += squares[row][col].constructor.name.charAt(0);
                            //         }
                            //     }
                            
                            //     if (take) {
                            //         move += (this.state.player === 1 ? this.cord[this.state.activePiecePos[1]] : this.cord[7 - this.state.activePiecePos[1]]);
                            //         move += 'x';
                            //         move += (this.state.player === 1 ? this.cord[col] : this.cord[7 - col]);
                            //     }
                            //     else {
                            //         move += (this.state.player === 1 ? this.cord[col] : this.cord[7 - col]);
                            //     }

                            //     move += (this.state.player === 1 ? 8 - row : row + 1)
                            //     if (check) {
                            //         move += '+'
                            //     }
                            // }

                            let tempMoves = JSON.parse(JSON.stringify(this.state.moves));
                            tempMoves.push(move);
                            
                            socket.emit('move', this.state.activePiecePos, ind, move);
                            socket.emit('changeTurn', this.state.turn);

                            this.setState({squares: squares, moves: tempMoves, turn: (this.state.player === 1 ? 2 : 1)});

                            
                            if (guard) {
                                socket.emit('move', rookFrom, rookTo);
                            }


                        }
                        else {
                            elem.style.position = "static";
                        }

                    }

                    //////////////////////////////////// CHECKED AND NOT KING ////////////////////////////////////

                    else {
                        // console.log('checked and NOT king')

                        squares[ind[0]][ind[1]] = squares[this.state.activePiecePos[0]][this.state.activePiecePos[1]];
                        squares[this.state.activePiecePos[0]][this.state.activePiecePos[1]] = null;

                        let checkedPos = [];
                        for (let i = 0; i < squares.length; i++) {
                            for(let j = 0; j < squares[i].length; j++) {
                                if (squares[i][j]) {
                                    if (squares[i][j].player !== this.state.player && !(squares[i][j] instanceof King)) {
                                        if (squares[i][j] instanceof Pawn) {
                                            checkedPos = checkedPos.concat(squares[i][j].getOppHitPossibleMoves([i, j], squares));
                                        }
                                        else {
                                            checkedPos = checkedPos.concat(squares[i][j].getPossibleMoves([i, j], squares));
                                        }
                                    }
                                }
                            }
                        }

                        if (!checkedPos.includes(this.kingYou.toString())) {
                            
                            if (squares[row][col] instanceof Pawn) {
                                if (row === 0) {
                                    this.setState({pawnPos: [row, col], pawn: true, squares: squares});

                                    elem.style.position = "static";
        
                                    return;
                                }
                            }

                            this.setState({squares: squares, checked: false, turn: (this.state.player === 1 ? 2 : 1)});
                            
                            socket.emit('move', this.state.activePiecePos, ind);
                            socket.emit('changeTurn', this.state.turn);    
                        }
                        else {
                            elem.style.position = "static";
                        }        

                    }

                }
                else {
                    elem.style.position = "static";
                }
                
                this.setState({activePiece: null, activePiecePos: null});

            }
            else {
                elem.style.position = "static";
                
                this.setState({activePiece: null, activePiecePos: null});
            }

        }

    }

    changePawn(piece) {
        var squares = [];
        for (var i = 0; i < this.state.squares.length; i++)
            squares[i] = this.state.squares[i].slice();

        squares[this.state.pawnPos[0]][this.state.pawnPos[1]] = (piece === 'queen' ? new Queen(this.state.player) : piece === 'rook' ? new Rook(this.state.player) : piece === 'knight' ? new Knight(this.state.player) : piece === 'bishop' ? new Bishop(this.state.player) : null);

        let move = this.state.pawnPos[2];
        if (piece === 'knight') {
            move += 'N';
        }
        else {
            move += piece.charAt(0).toUpperCase();
        }

        let tempMoves = JSON.parse(JSON.stringify(this.state.moves));
        tempMoves.push(move);

        socket.emit('move', this.state.activePiecePos, this.state.pawnPos, move);
        socket.emit('pawnChange', this.state.pawnPos, piece);
        socket.emit('changeTurn', this.state.turn);

        this.setState({squares: squares, pawn: false, activePiece: null, activePiecePos: null, moves: tempMoves, turn: (this.state.player === 1 ? 2 : 1)});
    }

    playAgain = () => {
        if (this.state.playAgain === 'Yes') { 
            socket.emit('reset');
        }
        else {
            socket.emit('playAgain');
            this.setState({playAgain: 'Wait'});
        }
    }

    handleClick(e) {
        // this.setState({boardSize: (isMobile ? window.innerWidth - 40 : Math.min(window.innerHeight - 160, window.innerWidth - 100))})
        if (!this.state.activePiecePos) {
            const elem = e.target;

            if (elem.classList.contains('piece')) {

                const row = Math.floor((e.clientY - document.getElementById('chessboard').offsetTop + window.scrollY) / (this.state.boardSize / 8));
                const col = Math.floor((e.clientX - document.getElementById('chessboard').offsetLeft + window.scrollX) / (this.state.boardSize / 8));

                if (this.state.squares[row][col] && this.state.squares[row][col].player === this.state.player && !this.state.result) {
                    const ind = [row, col];
                    
                    this.setState({activePiece: elem, activePiecePos: ind, boardSize: (isMobile ? window.innerWidth - 40 : Math.min(window.innerHeight - 160, window.innerWidth - 100))});
                }
            }
        }
        else {

            const elem = e.target;

            this.setState({activePieceHover: null});
    
            if (this.state.activePiecePos) {
    
                if (this.state.player === this.state.turn) {
    
                    const row = Math.floor((e.clientY + window.scrollY - document.getElementById('chessboard').offsetTop) / (this.state.boardSize / 8));
                    const col = Math.floor((e.clientX + window.scrollX - document.getElementById('chessboard').offsetLeft) / (this.state.boardSize / 8));
            
                    const ind = [row, col];
                    
                    if (this.state.squares[row][col] && this.state.squares[row][col].player === this.state.player) {
                        this.setState({activePiece: elem, activePiecePos: [row, col]});
                        return;
                    }

                    var squares = [];
                    for (var i = 0; i < this.state.squares.length; i++)
                        squares[i] = this.state.squares[i].slice();
    
                    let possibleMoves = squares[this.state.activePiecePos[0]][this.state.activePiecePos[1]].getPossibleMoves(this.state.activePiecePos, squares);
    
                    if (ind !== this.state.activePiecePos && possibleMoves.includes(ind.toString())) {
                        
                        //////////////////////////////////// CHECKED AND KING ////////////////////////////////////
    
                        if (this.state.checked && squares[this.state.activePiecePos[0]][this.state.activePiecePos[1]] instanceof King) {
                            // console.log('checked and king')
    
                            if (Math.abs(col - this.state.activePiecePos[1]) === 1) {
                                squares[row][col] = squares[this.state.activePiecePos[0]][this.state.activePiecePos[1]];
                                squares[this.state.activePiecePos[0]][this.state.activePiecePos[1]] = null;
                                
                                squares[row][col].moved = true;
    
                                this.setState({squares: squares, checked: false, turn: (this.state.player === 1 ? 2 : 1)});
                                this.kingYou = ind;
                                socket.emit('kingMove', ind);
    
                                socket.emit('move', this.state.activePiecePos, ind);
                                // socket.emit('changeTurn', this.state.turn);
                            }
                            else {
    
                            }
                        }
    
                        //////////////////////////////////// NOT CHECKED AND SOMETHING ////////////////////////////////////
    
                        else if (!this.state.checked) {
                            // console.log('not checked and something')
    
                            let take = (squares[row][col] ? true : false);
    
                            squares[row][col] = squares[this.state.activePiecePos[0]][this.state.activePiecePos[1]];
                            squares[this.state.activePiecePos[0]][this.state.activePiecePos[1]] = null;
                            
                            let checkedPos = [];
                            for (let i = 0; i < squares.length; i++) {
                                for(let j = 0; j < squares[i].length; j++) {
                                    if (squares[i][j]) {
                                        if (squares[i][j].player !== this.state.player && !(squares[i][j] instanceof King)) {
                                            if (squares[i][j] instanceof Pawn) {
                                                checkedPos = checkedPos.concat(squares[i][j].getOppHitPossibleMoves([i, j], squares));
                                            }
                                            else {
                                                checkedPos = checkedPos.concat(squares[i][j].getHitPossibleMoves([i, j], squares));
                                            }
                                        }
                                    }
                                }
                            }
        
                            let guard = null;
                            var rookFrom = [];
                            var rookTo = [];
    
                            if ((squares[row][col] instanceof King ? !checkedPos.includes(`${row},${col}`) : !checkedPos.includes(this.kingYou.toString()))) {
    
                                if (squares[row][col] instanceof King) {
                                    this.kingYou = [row, col]
                                    socket.emit('kingMove', [row, col]);
    
                                    squares[row][col].moved = true;
                                    
                                    if (Math.abs(col - this.state.activePiecePos[1]) > 1) {
                                        if (col > this.state.activePiecePos[1]) {
                                            squares[row][col - 1] = squares[row][7];
                                            squares[row][7] = null;
    
                                            rookFrom = [row, 7];
                                            rookTo = [row, col - 1];
                                        }
                                        else {
                                            squares[row][col + 1] = squares[row][0];
                                            squares[row][0] = null;
    
                                            rookFrom = [row, 0];
                                            rookTo = [row, col + 1];
                                        }
                                        guard = col - this.state.activePiecePos[1];
                                    }
                                }
    
                                let check = false;
                                if (!(squares[row][col] instanceof King)) {
                                    if (squares[ind[0]][ind[1]].getHitPossibleMoves([ind[0], ind[1]], squares).includes(this.kingOpponent.toString())) {
                                        // socket.emit('check', [row, col]);
                                        check = true;
                                    }
                                }
    
                                let move = '';
                                if (guard) {
                                    if (this.state.player === 1) {
                                        if (guard < 0) {
                                            move = 'o-o-o';
                                        }
                                        else {
                                            move = 'o-o';
                                        }
                                    }
                                    else {
                                        if (guard < 0) {
                                            move = 'o-o';
                                        }
                                        else {
                                            move = 'o-o-o';
                                        }
                                    }
                                }
                                else {
                                    // alert(squares[row][col].constructor.name)
                                    if (!(squares[row][col] instanceof Pawn)) {
                                        if (squares[row][col] instanceof Knight) {
                                            move += 'N';
                                        }
                                        else {
                                            move += squares[row][col].constructor.name.charAt(0);
                                        }
                                    }
                                
                                    if (take) {
                                        move += (this.state.player === 1 ? this.cord[this.state.activePiecePos[1]] : this.cord[7 - this.state.activePiecePos[1]]);
                                        move += 'x';
                                        move += (this.state.player === 1 ? this.cord[col] : this.cord[7 - col]);
                                    }
                                    else {
                                        move += (this.state.player === 1 ? this.cord[col] : this.cord[7 - col]);
                                    }
    
                                    move += (this.state.player === 1 ? 8 - row : row + 1)
                                    if (check) {
                                        move += '+'
                                    }
                                }
    
                                if (squares[row][col] instanceof Pawn) {
                                    if (row === 0) {
                                        this.setState({pawnPos: [row, col, move], pawn: true, squares: squares, turn: (this.state.player === 1 ? 2 : 1)});
    
                                        elem.style.position = "static";
            
                                        return;
                                    }
                                }
    
                                // let check = false;
                                // if (!(squares[row][col] instanceof King)) {
                                //     if (squares[ind[0]][ind[1]].getHitPossibleMoves([ind[0], ind[1]], squares).includes(this.kingOpponent.toString())) {
                                //         // socket.emit('check', [row, col]);
                                //         check = true;
                                //     }
                                // }
    
                                // let move = (guard ? guard === 2 ? 'o-o' : guard === 3 ? 'o-o-o' : null : );
                                // let move = '';
                                // if (guard) {
                                //     if (this.state.player === 1) {
                                //         if (guard < 0) {
                                //             move = 'o-o-o';
                                //         }
                                //         else {
                                //             move = 'o-o';
                                //         }
                                //     }
                                //     else {
                                //         if (guard < 0) {
                                //             move = 'o-o';
                                //         }
                                //         else {
                                //             move = 'o-o-o';
                                //         }
                                //     }
                                // }
                                // else {
                                //     // alert(squares[row][col].constructor.name)
                                //     if (!(squares[row][col] instanceof Pawn)) {
                                //         if (squares[row][col] instanceof Knight) {
                                //             move += 'N';
                                //         }
                                //         else {
                                //             move += squares[row][col].constructor.name.charAt(0);
                                //         }
                                //     }
                                
                                //     if (take) {
                                //         move += (this.state.player === 1 ? this.cord[this.state.activePiecePos[1]] : this.cord[7 - this.state.activePiecePos[1]]);
                                //         move += 'x';
                                //         move += (this.state.player === 1 ? this.cord[col] : this.cord[7 - col]);
                                //     }
                                //     else {
                                //         move += (this.state.player === 1 ? this.cord[col] : this.cord[7 - col]);
                                //     }
    
                                //     move += (this.state.player === 1 ? 8 - row : row + 1)
                                //     if (check) {
                                //         move += '+'
                                //     }
                                // }
    
                                let tempMoves = JSON.parse(JSON.stringify(this.state.moves));
                                tempMoves.push(move);
                                
                                socket.emit('move', this.state.activePiecePos, ind, move);
                                socket.emit('changeTurn', this.state.turn);
    
                                this.setState({squares: squares, moves: tempMoves, turn: (this.state.player === 1 ? 2 : 1)});
    
                                
                                if (guard) {
                                    socket.emit('move', rookFrom, rookTo);
                                }
    
    
                            }
                            else {
                                elem.style.position = "static";
                            }
    
                        }
    
                        //////////////////////////////////// CHECKED AND NOT KING ////////////////////////////////////
    
                        else {
                            // console.log('checked and NOT king')
    
                            squares[ind[0]][ind[1]] = squares[this.state.activePiecePos[0]][this.state.activePiecePos[1]];
                            squares[this.state.activePiecePos[0]][this.state.activePiecePos[1]] = null;
    
                            let checkedPos = [];
                            for (let i = 0; i < squares.length; i++) {
                                for(let j = 0; j < squares[i].length; j++) {
                                    if (squares[i][j]) {
                                        if (squares[i][j].player !== this.state.player && !(squares[i][j] instanceof King)) {
                                            if (squares[i][j] instanceof Pawn) {
                                                checkedPos = checkedPos.concat(squares[i][j].getOppHitPossibleMoves([i, j], squares));
                                            }
                                            else {
                                                checkedPos = checkedPos.concat(squares[i][j].getPossibleMoves([i, j], squares));
                                            }
                                        }
                                    }
                                }
                            }
    
                            if (!checkedPos.includes(this.kingYou.toString())) {
                                
                                if (squares[row][col] instanceof Pawn) {
                                    if (row === 0) {
                                        this.setState({pawnPos: [row, col], pawn: true, squares: squares});
    
                                        elem.style.position = "static";
            
                                        return;
                                    }
                                }
    
                                this.setState({squares: squares, checked: false, turn: (this.state.player === 1 ? 2 : 1)});
                                
                                socket.emit('move', this.state.activePiecePos, ind);
                                socket.emit('changeTurn', this.state.turn);    
                            }
                            else {
                                elem.style.position = "static";
                            }        
    
                        }
    
                    }
                    else {
                        elem.style.position = "static";
                    }
                    
                    this.setState({activePiece: null, activePiecePos: null});

                }
                else {
                    elem.style.position = "static";
                    
                    this.setState({activePiece: null, activePiecePos: null});
                }
    
            }
    
    
        }
    }


    render() {
        return (
            <div id="chessplay">

                <div id="cpresultcont" style={(!this.state.result ? {visibility: 'hidden'} : {})}>
                    <div id="cprescontrol" style={{backgroundImage: 'url(' + lpmainGrey + ')'}}>
                        {this.state.result === 'draw' && (
                            <label htmlFor=""><span>Draw</span></label>
                        )}
                        {this.state.result !== 'draw' && (
                            <label htmlFor="">You <span>{(this.state.result === 'win' ? 'won' : 'lost')}</span></label>
                        )}
                        <button id='pabtn' onClick={ this.playAgain }>Play again - { this.state.playAgain }</button>
                        <button id='gtmbtn' onClick={ this.props.goToMenu }>Go to menu</button>
                    </div>
                </div>
                
                <div id="cppawncont" style={(!this.state.pawn ? {visibility: 'hidden'} : {})}>
                    <div id="cppawn">

                        <div className='cppoption' onClick={() => this.changePawn('queen')}>
                            <img src={(this.state.player === 1 ? queenWhiteImg : queenBlackImg)} alt="" />
                        </div>
                        <div className='cppoption' onClick={() =>  this.changePawn('rook')}>
                            <img src={(this.state.player === 1 ? rookWhiteImg : rookBlackImg)} alt="" />
                        </div>
                        <div className='cppoption' onClick={() =>  this.changePawn('knight')}>
                            <img src={(this.state.player === 1 ? knightWhiteImg : knightBlackImg)} alt="" />
                        </div>
                        <div className='cppoption' onClick={() =>  this.changePawn('bishop')}>
                            <img src={(this.state.player === 1 ? bishopWhiteImg : bishopBlackImg)} alt="" />
                        </div>

                    </div>
                </div>

                {/* <div id="cpdrawcont" style={(!this.state.draw ? {visibility: 'hidden'} : {})}>
                    <div id='cpdraw'>
                        <h1>Draw ???</h1>
                        <div id='cpdrawbtncont'>
                            <button onClick={() => {this.setState({draw: false}); socket.emit('acceptDraw')}}>Accept</button>
                            <button onClick={() => {this.setState({draw: false}); socket.emit('declineDraw')}}>Decline</button>
                        </div>
                    </div>
                </div> */}

                <div id="cpresultcont" style={(!this.state.draw ? {visibility: 'hidden'} : {})}>
                    <div id="cprescontrol" style={{backgroundImage: 'url(' + lpmainGrey + ')'}}>
                        <label>Draw???</label>
                        <button id='pabtn' onClick={() => {this.setState({draw: false}); socket.emit('acceptDraw')}}>Accept</button>
                        <button id='gtmbtn' onClick={() => {this.setState({draw: false}); socket.emit('declineDraw')}}>Decline</button>
                    </div>
                </div>


                <div id="cpleft">
                    <div className={"player " + (this.state.turn !== this.state.player ? 'active' : '')}>
                        Opponent
                        <div className='time'>
                            { this.state.timeOppMin }:{ this.state.timeOppSec }
                        </div>
                    </div>

                    <ChessBoard squares={ this.state.squares } activePieceHover={ this.state.activePieceHover } activePiecePos={ this.state.activePiecePos } checked={ this.state.checked } kingYou={ this.kingYou } player={ this.state.player } onMouseDown = {(e) => this.handleMouseDown(e)} onMouseMove = {(e) => this.handleMouseMove(e)} onMouseUp = {(e) => this.handleMouseUp(e)} gay={this.state.gay} handleClick={(e) => this.handleClick(e)} />
                
                    <div className={"player " + (this.state.turn === this.state.player ? 'active' : '')}>
                        You
                        <div className='time'>
                            { this.state.timeYouMin }:{ this.state.timeYouSec }
                        </div>
                    </div>
                </div>

                <div id="cpright">
                    <div className={"player " + (this.state.turn !== this.state.player ? 'active' : '')}>
                        Opponent
                        <div className='time'>
                            { this.state.timeOppMin }:{ this.state.timeOppSec }
                        </div>
                    </div>

                    <div id='cpcontrol'>
                        <div id='cpctab'>
                            <label htmlFor="">Moves</label>
                            <div id='cpctsettings' className={(this.state.gay ? 'active ' : '')} onClick={() => {if (!this.state.gay) {this.setState({gay: true})} else {this.setState({gay: false})} }}>
                                
                                {/* <img src={ settings } alt="" /> */}
                            </div>
                        </div>
                        <div id='cpcmoves'>

                            <table>
                                <tbody>
                                    {this.state.moves.map((move, ind) => {
                                        return (
                                            ind % 2 === 0 && (
                                                <tr key={ ind }>
                                                    <td>{ ind / 2 + 1 }</td>
                                                    <td>{ this.state.moves[ind] }</td>
                                                    <td>{ this.state.moves[ind + 1]}</td>
                                                </tr>
                                            )
                                        )
                                    })}
                                </tbody>
                            </table>

                        </div>
                        <div id='cpcbtncont'>
                            <button className={(this.state.drawRequest ? 'active ' : '')} onClick={() => {this.setState({drawRequest: true}); socket.emit('drawRequest')}}>Draw</button>
                            <button onClick={() => {socket.emit('refresh'); this.props.goToMenu()}}>Leave</button>
                        </div>
                    </div>

                    <div className={"player " + (this.state.turn === this.state.player ? 'active' : '')}>
                        You
                        <div className='time'>
                            { this.state.timeYouMin }:{ this.state.timeYouSec }
                        </div>
                    </div>

                </div>

            </div>
        )
    }
}


export default ChessPlay;
