import React, { Component, useState, useEffect, useContext } from 'react';

import { SocketContext, socket } from '../../../context/socketTTT';

import './TTTPlay.css';

import x from '../../../assets/ttt/x.svg';
import o from '../../../assets/ttt/o.svg';

import lpmainGrey from '../../../assets/lpmain-grey.svg';

import Cell from './Cell';

const Square = (props) => {

    let tempGrid = [];
    for (let i = 0; i < 3; i++) {
        tempGrid.push(props.grid[i].slice());
    }

    const handleMouseEnter = () => {
        if (!tempGrid[props.row][props.col].val) {
            tempGrid[props.row][props.col].hover = true;
            props.setGrid(tempGrid);
        }
    }
    const handleMouseLeave = () => {
        if (!tempGrid[props.row][props.col].val) {
            tempGrid[props.row][props.col].hover = false;
            props.setGrid(tempGrid);
        }
    }

    return (
        <div key={ props.id } className='cell' onClick={() => props.handleCellClick(props.row, props.col)} onMouseEnter={ handleMouseEnter } onMouseLeave={ handleMouseLeave }>
            {/* <div className={'cellHover ' + (props.hover ? 'hover ' : '')}>{ props.shape }</div>
            <div className='cellShape'>{(props.val ? props.val : '')}</div> */}
            <div className={'cellHover ' + (props.hover ? 'hover ' : '')}><img src={ props.shape } /></div>
            <div className='cellShape'>{(props.val ? <img src={ props.val } /> : '')}</div>
        </div>
    )
}

const emptyGrid = [[new Cell(), new Cell(), new Cell()],
                   [new Cell(), new Cell(), new Cell()],
                   [new Cell(), new Cell(), new Cell()]]


class TTTPlay extends Component {

    static socket = socket;

    constructor(props) {
        super(props);
        this.state = {
            grid: JSON.parse(JSON.stringify(emptyGrid)),
            turn: 1,
            result: null,

            playAgain: 'No',
        }

        this.shape = props.shape;
        this.oppShape = props.oppShape;

        this.player = props.player;

        socket.on('move', (row, col) => {
            let tempGrid = JSON.parse(JSON.stringify(this.state.grid));
            tempGrid[row][col].val = this.oppShape;

            this.setState({grid: tempGrid});
        });
        socket.on('changeTurn', () => {
            console.log('changeTurn');
            this.setState({turn: this.player});
        });
        socket.on('lose', () => {
            this.setState({result: 'lose'});
        })
        socket.on('draw', () => {
            this.setState({result: 'draw'});
        })
        socket.on('playAgainReady', () => {
            this.setState({playAgain: 'Yes'});
        });
        socket.on('playAgain', () => {
            this.setState({grid: JSON.parse(JSON.stringify(emptyGrid)), result: null, turn: 1, playAgain: 'No'});
        });

    }


    
    checkWin = (grid) => {
        const lines = [
            [[0, 0], [0, 1], [0, 2]],
            [[1, 0], [1, 1], [1, 2]],
            [[2, 0], [2, 1], [2, 2]],
            [[0, 0], [1, 0], [2, 0]],
            [[0, 1], [1, 1], [2, 1]],
            [[0, 2], [1, 2], [2, 2]],
            [[0, 0], [1, 1], [2, 2]],
            [[0, 2], [1, 1], [2, 0]],
        ];
        for (let i = 0; i < lines.length; i++) {
            const [[a1, a2], [b1, b2], [c1, c2]] = lines[i];
            if (grid[a1][a2].val && grid[a1][a2].val === grid[b1][b2].val && grid[a1][a2].val === grid[c1][c2].val) {
              return true;
            }
          }
          return false;
    }

    handleCellClick = (row, col) => {
        // let name = this.shape.split('/').pop().split('.')[0];
        if (this.player === this.state.turn && !this.state.result && !this.state.grid[row][col].val) {
            let tempGrid = [];
            for (let i = 0; i < 3; i++) {
                tempGrid.push(this.state.grid[i].slice());
            }

            tempGrid[row][col].val = this.shape;
            
            if (this.checkWin(tempGrid)) {
                this.setState({grid: tempGrid, result: 'win'});
                socket.emit('lose');
            }
            else {
                let filled = true;
                for (let i = 0; i < 3; i++) {
                    for (let j = 0; j < 3; j++) {
                        if (!tempGrid[i][j].val) {
                            filled = false;
                            break
                        }
                    }
                }

                if (filled) {
                    this.setState({grid: tempGrid, result: 'draw', turn: (this.player === 1 ? 2 : 1)});
                    socket.emit('draw');    
                }
                else {
                    this.setState({grid: tempGrid, turn: (this.player === 1 ? 2 : 1)});
                }

            }

            socket.emit('move', row, col);
            socket.emit('changeTurn');
        }
    }

    playAgain = () => {
        if (this.state.playAgain === 'Yes') {
            this.setState({grid: JSON.parse(JSON.stringify(emptyGrid)), result: null, turn: 1, playAgain: 'No'});

            socket.emit('playAgain');
        }
        else {
            this.setState({playAgain: 'Wait'})
            socket.emit('playAgainReady');
        }
    }

    setGrid = (tempGrid) => {
        this.setState({grid: tempGrid});
    }

    render() {
        const board = [];
        for (let i = 0; i < 3; i++) {
            let row = [];
            for (let j = 0; j < 3; j++) {
                row.push(<Square id={ i * 3 + j } pos={ [i, j] } row={ i } col={ j } grid={ this.state.grid } val={ this.state.grid[i][j].val } shape={ this.shape } handleCellClick={ this.handleCellClick } hover={ this.state.grid[i][j].hover } setGrid={ this.setGrid } />);
            }
            board.push(row);
        }
    
        return (
            <div id='tttplay'>
                <div id="cpresultcont" style={(!this.state.result ? {visibility: 'hidden'} : {})}>
                    <div id="cprescontrol" style={{backgroundImage: 'url(' + lpmainGrey + ')'}}>
                        {/* <label htmlFor="">You <span>{(this.state.result === 'win' ? 'won' : this.state.result === 'draw' ? 'draw' : 'lost')}</span></label> */}
                        {/* <label htmlFor="">{this.state.result === 'draw' ? <span>Draw</span> : <span>(this.state.result === 'win' ? 'won' : 'lost')</span>}</label> */}
                        {this.state.result === 'draw' && (
                            <label htmlFor=""><span>Draw</span></label>
                        )}
                        {this.state.result !== 'draw' && (
                            <label htmlFor="">You <span>{(this.state.result === 'win' ? 'won' : 'lost')}</span></label>
                        )}
                        <button id='pabtn' onClick={ this.playAgain }>Play again - { this.state.playAgain }</button>
                        <button id='gtmbtn' onClick={ this.props.goToMenu }>Go to menu</button>
                    </div>
                </div>

                <div className={'player ' + (this.state.turn === this.player ? '' : 'active ')}>
                    Opponent
                    <div>
                        <img src={ this.oppShape } alt="" />
                    </div>
                </div>

                <div id='board'>
                    { board }
                </div>

                <div className={'player ' + (this.state.turn === this.player ? 'active ' : ' ')}>
                    You
                    <div>
                        <img src={ this.shape } alt="" />
                    </div>
                </div>
                
                {/* <div id='gamestats'>
                    <div className={'player ' + (this.state.turn === this.player ? '' : 'active ')}>
                        Opponent
                        <div>
                            <img src={ this.oppShape } alt="" />
                        </div>
                    </div>
                    <div id='gsdesc'>

                    </div>
                    <div className={'player ' + (this.state.turn === this.player ? 'active ' : '')}>
                        You
                        <div>
                            <img src={ this.shape } alt="" />
                        </div>
                    </div>

                </div> */}
            </div>
        );

    }

}



export default TTTPlay;
