import React, { Component, useState, useEffect } from 'react';
import { useParams } from 'react-router';
import { useHistory } from 'react-router-dom';

import { SocketContext, socket } from '../../context/socketTTT';

import './TTT.css';

import TTTConnect from './TTTConnect/TTTConnect';
import TTTPlay from './TTTPlay/TTTPlay';

import { isMobile } from 'react-device-detect';

import x from '../../assets/ttt/x.svg';
import o from '../../assets/ttt/x.svg';

const TTT = () => {

    let { id } = useParams();

    const [load, setLoad] = useState('connect');

    const [shape, setShape] = useState(null);
    const [oppShape, setOppShape] = useState(null);

    const [player, setPlayer] = useState(null);

    // const [load, setLoad] = useState('play');
    // const [shape, setShape] = useState(x);
    // const [oppShape, setOppShape] = useState(o);
    // const [player, setPlayer] = useState(1);


    const [windowWidth, setWindowWidth] = useState(window.innerWidth);
    const [windowHeight, setWindowHeight] = useState(window.innerHeight);
    const [windowScroll, setWindowScroll] = useState(window.scrollY);
    
    const handleResize = () => {
        setWindowWidth(window.innerWidth);
        setWindowHeight(window.innerHeight);
    }
    window.addEventListener('resize', handleResize);

    const handleScroll = () => {
        setWindowScroll(window.scrollY);
    }
    window.addEventListener('scroll', handleScroll);

    var firstShape;
    useEffect(() => {
        firstShape = shape;
        return () => {
            
        }
    }, [])

    const changeShape = (shape) => {
        setShape(shape);
    }
    
    const changeOppShape = (shape) => {
        setOppShape(shape);
    }

    const changePlayer = (player) => {
        setPlayer(player);
    }

    const startGame = () => {
        setLoad('play');
    }

    let history = useHistory();
    const goToMenu = () => {
        if (id) {
            history.push('/TTT');
        }
        setLoad('connect');
        socket.emit('refresh');
    }

    return (
        <SocketContext.Provider value={ socket }>
            <div id="ttt" className={ windowWidth < window.screen.width - windowHeight - 160 + 50 + ((windowHeight - 160) / 1.875) + 50 ? 'mid ' : '' + (isMobile ? 'mobile ' : '')}>
                {/* <img src={ shape } alt="" />
                <label htmlFor="" style={{color: 'white'}}>{ player }</label>
                <img src={ oppShape } alt="" /> */}
                {load === 'connect' && <TTTConnect id={ id } changeShape={ changeShape } changeOppShape={ changeOppShape } changePlayer={ changePlayer } firstShape={ firstShape } shape={ shape } oppShape={ oppShape } startGame={ startGame } />}
                {load === 'play' && <TTTPlay shape={ shape } oppShape={ oppShape } player={ player } goToMenu={ goToMenu } />}
                {/* {load === 'play' && <ChessPlay room={ room } player={ player } mode={ mode } />} */}
            </div>
        </SocketContext.Provider>

    );
}
 
export default TTT;


