import React, { Component, useState } from 'react';
import { useParams } from 'react-router';

import { useHistory } from 'react-router-dom';

import { SocketContext, socket } from '../../context/socketChess';

import { isMobile } from 'react-device-detect';

import './Chess.css';

import ChessConnect from './ChessConnect/ChessConnect';
import ChessPlay from './ChessPlay/ChessPlay';

const Chess = () => {

    let { id } = useParams();

    const [load, setLoad] = useState('connect');

    const [player, setPlayer] = useState(null);
    const [mode, setMode] = useState(null);

    // const [load, setLoad] = useState('play');

    // const [player, setPlayer] = useState(1);
    // const [mode, setMode] = useState('10/00');


    const [windowWidth, setWindowWidth] = useState(window.innerWidth);
    const [windowHeight, setWindowHeight] = useState(window.innerHeight);
    const [windowScroll, setWindowScroll] = useState(window.scrollY);
    
    const handleResize = () => {
        setWindowWidth(window.innerWidth);
        setWindowHeight(window.innerHeight);
    }
    window.addEventListener('resize', handleResize)

    const handleScroll = () => {
        setWindowScroll(window.scrollY);
    }
    window.addEventListener('scroll', handleScroll);

    const changePlayer = (player) => {
        setPlayer(player);
    }
    const changeMode = (mode) => {
        setMode(mode);
    }

    let history = useHistory();
    const goToMenu = () => {
        if (id) {
            history.push('/Chess');
        }
        setLoad('connect');
        socket.emit('refresh');
    }

    const startGame = () => {
        setLoad('play');
    }

    return (
        <SocketContext.Provider value={ socket }>
            <div id="chess" className={ windowWidth < window.screen.width - windowHeight - 160 + 50 + ((windowHeight - 160) / 1.875) + 50 ? 'mid ' : '' + (isMobile ? 'mobile ' : '')}>
                {/* { scroll } */}
                {/* { window.screen.width }
                { window.screen.width - windowHeight - 160 + 50 + ((windowHeight - 160) / 1.875) + 50 } */}

                {/* <label style={{color: 'white'}}>{ player } ----- { mode } --------- { room }</label> */}
                {load === 'connect' && <ChessConnect id={ id } startGame={ startGame } changePlayer={ changePlayer } changeMode={ changeMode } goToMenu={ goToMenu } windowWidth={ windowWidth } windowHeight={ windowHeight } />}
                {load === 'play' && <ChessPlay player={ player } mode={ mode } goToMenu={ goToMenu } windowWidth={ windowWidth } windowHeight={ windowHeight } windowScroll={ windowScroll } />}
            </div>
        </SocketContext.Provider>
    );
}
 
export default Chess;
