import React, { Component, useRef, useState } from 'react';

import './DrawSpot.css';

import ReactDOM from "react-dom";
import CanvasDraw from "react-canvas-draw";

import tinycolor from 'tinycolor2';

import pen from '../../../../../assets/ttt/pen.svg';
import eraser from '../../../../../assets/ttt/eraser.svg';
import brush from '../../../../../assets/ttt/brush.svg';
import letter from '../../../../../assets/ttt/letter.svg';
import colorImg from '../../../../../assets/ttt/color.svg';

import thick from '../../../../../assets/ttt/thick.svg';

import tick from '../../../../../assets/ttt/tick.svg';
import close from '../../../../../assets/ttt/close.svg';

import x from '../../../../../assets/ttt/x.svg';
import o from '../../../../../assets/ttt/o.svg';

import colorX from '../../../../../assets/ttt/colorX.svg';
import { isMobile } from 'react-device-detect';

var re = /^#([0-9A-F]{3}){1,2}$/i;

const DrawSpot = (props) => {

    const [tool, setTool] = useState('pen');

    const [popUp, setPopUp] = useState(null);

    const [color, setColor] = useState('#FFFFFF');
    const [hex, setHex] = useState(null);

    const [thickness, setThickness] = useState(1);
    const [customThickness, setCustomThickness] = useState(null);

    const [ready, setReady] = useState(false);

    const [img, setImg] = useState(null);

    const canvas = useRef();

    const handleEraseClick = () => {
        canvas.current.clear();
        setReady(false);
    }

    const handleThicknessClick = (thickness) => {
        setThickness(thickness);
    }

    const handleCanvasChange = () => {
        if (!ready) {
            setReady(true);
        }
        if (props.selected) {
            props.handleCustomShapeChange(canvas.current.getDataURL());
        }
    }

    const handleUseClick = () => {
        if (!props.selected) {
            props.handleCustomShapeChange(canvas.current.getDataURL());
        }
        else {
            props.handleShapeChange((props.player === 1 ? x : o));
        }
    }

    const changeColor = (color) => {
        setColor(color);
        setPopUp(null);
    }

    const handleHexSubmit = () => {
        if (hex) {
            var c = hex;

            var tempHex = (hex.charAt(0) === '#' ? hex : '#' + hex);
            // if (hex.charAt(0) !== '#') {
            //     setHex('#' + hex);
            // }
            // if (hex.charAt(0) === '#') {
            //     c = hex.substring(1);
            // }

            var c = tempHex.substring(1);      // strip #
            var rgb = parseInt(c, 16);   // convert rrggbb to decimal
            var r = (rgb >> 16) & 0xff;  // extract red
            var g = (rgb >>  8) & 0xff;  // extract green
            var b = (rgb >>  0) & 0xff;  // extract blue

            var luma = 0.2126 * r + 0.7152 * g + 0.0722 * b; // per ITU-R BT.709

            if (re.test(hex) && luma >= 40) {
                setColor(hex);
                setPopUp(null);
            }
            // console.log(tinycolor(tempHex).isLight())

            // if (re.test(tempHex)) {
            //     setColor(tempHex);
            //     setPopUp(null);
            // }

            // if (re.test(tempHex) && tinycolor(tempHex).isLight()) {
            //     setColor(tempHex);
            //     setPopUp(null);
            // }
        
        }
    }

    return (
        <div id='drawspot'>

            <button id='usebtn' className={(ready ? 'ready ' : '') + (props.selected ? 'active ' : '')} onClick={ handleUseClick } ><img src={(props.selected ? close : tick)} /></button>
            
            <div className='dtoolbar'>
                <div className={'dtboption ' + (tool === 'pen' ? 'active ' : '')}>
                    <img src={ pen } alt="" />
                </div>
                <div className={'dtboption ' + (tool === 'eraser' ? 'active ' : '')} onClick={ handleEraseClick }>
                    <img src={ eraser } alt="" />
                </div>
                <div className='dtboption'>
                    <img src={ brush } alt="" />
                </div>
                <div className='dtboption'>
                    <img src={ letter } alt="" />
                </div>
                <div className={'dtboption ' + (popUp === 'color' ? 'popup ' : '')}>
                    <div className='dtboicon' onClick={() => {if (popUp !== 'color') {setPopUp('color')} else {setPopUp(null)}}}>
                        <div className='dtbocolor' style={{backgroundColor: color}}></div>
                    </div>

                    <div className='popup right' style={(popUp === 'color' ? {display: 'block'} : {})}>
                        <div className='putop'>
                            <div className='pucolor' style={{backgroundColor: '#FFFFFF'}} onClick={() => changeColor('#FFFFFF')}></div>
                            <div className='pucolor' style={{backgroundColor: '#FFF000'}} onClick={() => changeColor('#FFF000')}></div>
                            <div className='pucolor' style={{backgroundColor: '#5AA9E6'}} onClick={() => changeColor('#5AA9E6')}></div>
                            <div className='pucolor' style={{backgroundColor: '#FF00C7'}} onClick={() => changeColor('#FF00C7')}></div>
                            <div className='pucolor' style={{backgroundColor: '#00F000'}} onClick={() => changeColor('#00F000')}></div>
                        </div>
                        <div className='pubottom'>
                            <div className='puinput'>
                                <input type="text" placeholder='Hex' onChange={(e) => {setHex(e.target.value)}}/>
                                <button className='puinputbtn'>
                                    <div className='pucolor' style={(re.test((hex ? (hex.charAt(0) === '#' ? hex : '#' + hex ) : '')) ? {backgroundColor: ((hex ? (hex.charAt(0) === '#' ? hex : '#' + hex ) : ''))} : {backgroundColor: 'white'})} onClick={ handleHexSubmit }>{(!re.test((hex ? (hex.charAt(0) === '#' ? hex : '#' + hex ) : '')) ? <img src={ colorX } /> : '')}</div>

                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div id='dcanvas'>
                <CanvasDraw 
                    ref={ canvas }
                    canvasWidth={ (isMobile ? 240 : 210) } 
                    canvasHeight={ 200 }
                    onChange={ handleCanvasChange } 
                    hideGrid={ true } 
                    brushColor={ color } 
                    backgroundColor='black' 
                    catenaryColor={ color }
                    lazyRadius={0} 
                    brushRadius={ thickness } 
                    disabled={ (thickness > 0 && tool === 'pen' ? false : true) }
                    hideInterface={ (thickness > 0 ? false : true) }
                    />
            </div>

            <div className='dtoolbar'>


                <div className={'dtboption ' + (popUp === 'thick' ? 'popup ' : '') + (thickness !== 1 && thickness !== 2 && thickness !== 3 && thickness !== 4 ? 'active ' : '')}>
                    <div className='dtboicon' onClick={() => {if (popUp !== 'thick') {setPopUp('thick')} else {setPopUp(null)}}}>
                        <img src={ thick } alt="" />
                    </div>
                    <div className='popup left' style={(popUp === 'thick' ? {display: 'block'} : {})}>
                        <div className='puthick'>
                            <input type="text" placeholder='Custom' onChange={(e) => setCustomThickness(e.target.value)} />
                            <button onClick={() => {if (!isNaN(customThickness)) {setThickness(Number(customThickness)); setPopUp(null)}}}>Set</button>
                        </div>
                    </div>
                </div>

                <div className={'dtboption ' + (thickness === 1 ? 'active ' : '')} onClick={() => handleThicknessClick(1)}>
                    <div className='dtbothick'>
                        <label>1<span>px</span></label>
                        <svg width="24" height="4" viewBox="0 0 24 4" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M1 1.44678C3.28342 0.990095 9.08329 0.487744 14.0155 2.1318C18.9476 3.77586 22.0074 2.81683 22.9208 2.1318" stroke="black" strokeLinecap="round"/>
                        </svg>
                    </div>
                </div>
                <div className={'dtboption ' + (thickness === 2 ? 'active ' : '')} onClick={() => handleThicknessClick(2)}>
                    <div className='dtbothick'>
                        <label>2<span>px</span></label>
                        <svg width="24" height="5" viewBox="0 0 24 5" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M1 1.44678C3.28342 0.990095 9.08329 0.487744 14.0155 2.1318C18.9476 3.77586 22.0074 2.81683 22.9208 2.1318" stroke="black" strokeWidth="2" strokeLinecap="round"/>
                        </svg>
                    </div>
                </div>
                <div className={'dtboption ' + (thickness === 3 ? 'active ' : '')} onClick={() => handleThicknessClick(3)}>
                    <div className='dtbothick'>
                        <label>3<span>px</span></label>
                        <svg width="26" height="6" viewBox="0 0 26 6" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M2 2.44678C4.28342 1.9901 10.0833 1.48774 15.0155 3.1318C19.9476 4.77586 23.0074 3.81683 23.9208 3.1318" stroke="black" strokeWidth="3" strokeLinecap="round"/>
                        </svg>
                    </div>
                </div>
                <div className={'dtboption ' + (thickness === 4 ? 'active ' : '')} onClick={() => handleThicknessClick(4)}>
                    <div className='dtbothick'>
                        <label>4<span>px</span></label>
                        <svg width="26" height="7" viewBox="0 0 26 7" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M2 2.44678C4.28342 1.9901 10.0833 1.48774 15.0155 3.1318C19.9476 4.77586 23.0074 3.81683 23.9208 3.1318" stroke="black" strokeWidth="4" strokeLinecap="round"/>
                        </svg>
                    </div>
                </div>
            </div>


            <div className='dstoolbar'>
                <div className='dstoption' onClick={ handleEraseClick }>
                    <img src={ eraser } />
                </div>

                <div className={'dstoption ' + (popUp === 'color' ? 'popup ' : '')}>
                    <div className='dtboicon' onClick={() => {if (popUp !== 'color') {setPopUp('color')} else {setPopUp(null)}}}>
                        <div className='dtbocolor' style={{backgroundColor: color}}></div>
                    </div>

                    <div className='popup right' style={(popUp === 'color' ? {display: 'block'} : {})}>
                        <div className='putop'>
                            <div className='pucolor' style={{backgroundColor: '#FFFFFF'}} onClick={() => changeColor('#FFFFFF')}></div>
                            <div className='pucolor' style={{backgroundColor: '#FFF000'}} onClick={() => changeColor('#FFF000')}></div>
                            <div className='pucolor' style={{backgroundColor: '#5AA9E6'}} onClick={() => changeColor('#5AA9E6')}></div>
                            <div className='pucolor' style={{backgroundColor: '#FF00C7'}} onClick={() => changeColor('#FF00C7')}></div>
                            <div className='pucolor' style={{backgroundColor: '#00F000'}} onClick={() => changeColor('#00F000')}></div>
                        </div>
                        {/* <div className='pubottom'>
                            <div className='puinput'>
                                <input type="text" placeholder='Hex' onChange={(e) => {setHex(e.target.value)}}/>
                                <button className='puinputbtn'>
                                    <div className='pucolor' style={(re.test((hex ? (hex.charAt(0) === '#' ? hex : '#' + hex ) : '')) ? {backgroundColor: ((hex ? (hex.charAt(0) === '#' ? hex : '#' + hex ) : ''))} : {backgroundColor: 'white'})} onClick={ handleHexSubmit }>{(!re.test((hex ? (hex.charAt(0) === '#' ? hex : '#' + hex ) : '')) ? <img src={ colorX } /> : '')}</div>

                                </button>
                            </div>
                        </div> */}
                    </div>
                </div>

                <div className={'dstoption ' + (thickness === 3 ? 'active ' : '')} onClick={() => handleThicknessClick(3)}>
                    <div className='dtbothick'>
                        <label>3<span>px</span></label>
                        <svg width="26" height="6" viewBox="0 0 26 6" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M2 2.44678C4.28342 1.9901 10.0833 1.48774 15.0155 3.1318C19.9476 4.77586 23.0074 3.81683 23.9208 3.1318" stroke="black" strokeWidth="3" strokeLinecap="round"/>
                        </svg>
                    </div>
                </div>
                <div className={'dstoption ' + (thickness === 2 ? 'active ' : '')} onClick={() => handleThicknessClick(2)}>
                    <div className='dtbothick'>
                        <label>2<span>px</span></label>
                        <svg width="24" height="5" viewBox="0 0 24 5" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M1 1.44678C3.28342 0.990095 9.08329 0.487744 14.0155 2.1318C18.9476 3.77586 22.0074 2.81683 22.9208 2.1318" stroke="black" strokeWidth="2" strokeLinecap="round"/>
                        </svg>
                    </div>
                </div>                
                <div className={'dstoption ' + (thickness === 1 ? 'active ' : '')} onClick={() => handleThicknessClick(1)}>
                    <div className='dtbothick'>
                        <label>1<span>px</span></label>
                        <svg width="24" height="4" viewBox="0 0 24 4" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M1 1.44678C3.28342 0.990095 9.08329 0.487744 14.0155 2.1318C18.9476 3.77586 22.0074 2.81683 22.9208 2.1318" stroke="black" strokeLinecap="round"/>
                        </svg>
                    </div>
                </div>

                <div className={'dstoption ' + (popUp === 'thick' ? 'popup ' : '') + (thickness !== 1 && thickness !== 2 && thickness !== 3 && thickness !== 4 ? 'active ' : '')}>
                    <div className='dtboicon' onClick={() => {if (popUp !== 'thick') {setPopUp('thick')} else {setPopUp(null)}}}>
                        <img src={ thick } alt="" />
                    </div>
                    <div className='popup left' style={(popUp === 'thick' ? {display: 'block'} : {})}>
                        <div className='puthick'>
                            <input type="text" placeholder='Custom' onChange={(e) => setCustomThickness(e.target.value)} />
                            <button onClick={() => {if (!isNaN(customThickness)) {setThickness(Number(customThickness)); setPopUp(null)}}}>Set</button>
                        </div>
                    </div>
                </div>
            </div>


        </div>
    );
}

export default DrawSpot;

