import Piece from "./Piece";

import King from "./King";

import pawnWhite from '../../../../assets/chess/pawn-white.svg'
import pawnBlack from '../../../../assets/chess/pawn-black.svg'

class Pawn extends Piece {
    constructor(player) {
        super(player, (player === 1 ? pawnWhite : pawnBlack));
        this.initialPos = {
            1: ['6,0', '6,1', '6,2', '6,3', '6,4', '6,5', '6,6', '6,7'],
            // 2: [8, 9, 10, 11, 12, 13, 14, 15]
        }
    }

    getPossibleMoves(startI, squares) {

        let possiblePos = []

        const row = startI[0];
        const col = startI[1];

        if (row - 1 >= 0 && squares[row - 1][col] === null) {
            // possiblePos.push([row - 1, col]);
            possiblePos.push(`${row - 1},${col}`);
        }
        
        if (this.initialPos[1].includes(startI.toString()) && squares[row - 1][col] === null && squares[row - 2][col] === null) {
            possiblePos.push(`${row - 2},${col}`);
        }
        if (row > 0) {
            if (col > 0 && squares[row - 1][col - 1] !== null && squares[row - 1][col - 1].player !== this.player) {
                possiblePos.push(`${row - 1},${col - 1}`);
            }
            if (col < 7 && squares[row - 1][col + 1] !== null && squares[row - 1][col + 1].player !== this.player) {
                possiblePos.push(`${row - 1},${col + 1}`);
            }
        }

        return possiblePos;

    }

    getHitPossibleMoves(startI, squares) {

        let possiblePos = []

        const row = startI[0];
        const col = startI[1];

        if (row > 0) {
            if (col > 0) {
                possiblePos.push(`${row - 1},${col - 1}`);
            }
            if (col < 7) {
                possiblePos.push(`${row - 1},${col + 1}`);
            }
        }

        return possiblePos;
    }

    getOppHitPossibleMoves(startI, squares) {
        
        let possiblePos = []

        const row = startI[0];
        const col = startI[1];

        if (row < 7) {
            if (col > 0) {
                possiblePos.push(`${row + 1},${col - 1}`);
            }
            if (col < 7) {
                possiblePos.push(`${row + 1},${col + 1}`);
            }
        }

        return possiblePos;
    }

    getHitPartPossibleMoves(startI, squares) {

        let possiblePos = []

        const row = startI[0];
        const col = startI[1];

        if (row > 0) {
            if (col > 0) {
                possiblePos.push(`${row - 1},${col - 1}`);
            }
            if (col < 7) {
                possiblePos.push(`${row - 1},${col + 1}`);
            }
        }

        return possiblePos;
    }
}


export default Pawn;
