import Piece from "./Piece";

import King from "./King";

import knightWhite from '../../../../assets/chess/knight-white.svg'
import knightBlack from '../../../../assets/chess/knight-black.svg'

class Knight extends Piece {
    constructor(player) {
        super(player, (player === 1 ? knightWhite : knightBlack));
        this.initialPos = {
            1: [1, 6],
            2: [57, 62]
        }
    }

    getPossibleMoves(startI, squares) {

        let possiblePos = []

        const row = startI[0];
        const col = startI[1];


        if (col - 2 >= 0) {
            if (row - 1 >= 0) {
                if (squares[row - 1][col - 2] === null || (squares[row - 1][col - 2] !== null && squares[row - 1][col - 2].player !== this.player)) {
                    possiblePos.push(`${row - 1},${col - 2}`);
                }
            }
            if (row + 1 < 8) {
                if (squares[row + 1][col - 2] === null || (squares[row + 1][col - 2] !== null && squares[row + 1][col - 2].player !== this.player)) {
                    possiblePos.push(`${row + 1},${col - 2}`);
                }
            }
        }

        if (col + 2 < 8) {
            if (row - 1 >= 0) {
                if (squares[row - 1][col + 2] === null || (squares[row - 1][col + 2] !== null && squares[row - 1][col + 2].player !== this.player )) {
                    possiblePos.push(`${row - 1},${col + 2}`);
                }
            }
            if (row + 1 < 8) {
                if (squares[row + 1][col + 2] === null || (squares[row + 1][col + 2] !== null && squares[row + 1][col + 2].player !== this.player)) {
                    possiblePos.push(`${row + 1},${col + 2}`);
                }
            }
        }

        if (row - 2 >= 0) {
            if (col - 1 >= 0) {
                if (squares[row - 2][col - 1] === null || (squares[row - 2][col - 1] !== null && squares[row - 2][col - 1].player !== this.player)) {
                    possiblePos.push(`${row - 2},${col - 1}`);
                }
            }
            if (col + 1 < 8) {
                if (squares[row - 2][col + 1] === null || (squares[row - 2][col + 1] !== null && squares[row - 2][col + 1].player !== this.player)) {
                    possiblePos.push(`${row - 2},${col + 1}`);
                }
            }
        }
        
        if (row + 2 < 8) {
            if (col - 1 >= 0) {
                if (squares[row + 2][col - 1] === null || (squares[row + 2][col - 1] !== null && squares[row + 2][col - 1].player !== this.player)) {
                    possiblePos.push(`${row + 2},${col - 1}`);
                }
            }
            if (col + 1 < 8) {
                if (squares[row + 2][col + 1] === null || (squares[row + 2][col + 1] !== null && squares[row + 2][col + 1].player !== this.player)) {
                    possiblePos.push(`${row + 2},${col + 1}`);
                }
            }
        }

        
        return possiblePos;

    }

    getHitPossibleMoves(startI, squares) {

        let possiblePos = []

        const row = startI[0];
        const col = startI[1];


        if (col - 2 >= 0) {
            if (row - 1 >= 0) {
                possiblePos.push(`${row - 1},${col - 2}`);
            }
            if (row + 1 < 8) {
                possiblePos.push(`${row + 1},${col - 2}`);
            }
        }

        if (col + 2 < 8) {
            if (row - 1 >= 0) {
                possiblePos.push(`${row - 1},${col + 2}`);
            }
            if (row + 1 < 8) {
                possiblePos.push(`${row + 1},${col + 2}`);
            }
        }

        if (row - 2 >= 0) {
            if (col - 1 >= 0) {
                possiblePos.push(`${row - 2},${col - 1}`);
            }
            if (col + 1 < 8) {
                possiblePos.push(`${row - 2},${col + 1}`);
            }
        }
        
        if (row + 2 < 8) {
            if (col - 1 >= 0) {
                possiblePos.push(`${row + 2},${col - 1}`);
            }
            if (col + 1 < 8) {
                possiblePos.push(`${row + 2},${col + 1}`);
            }
        }

        
        return possiblePos;

    }

    getHitPartPossibleMoves(startI, squares) {

        let possiblePos = []

        const row = startI[0];
        const col = startI[1];


        if (col - 2 >= 0) {
            if (row - 1 >= 0) {
                possiblePos.push(`${row - 1},${col - 2}`);
            }
            if (row + 1 < 8) {
                possiblePos.push(`${row + 1},${col - 2}`);
            }
        }

        if (col + 2 < 8) {
            if (row - 1 >= 0) {
                possiblePos.push(`${row - 1},${col + 2}`);
            }
            if (row + 1 < 8) {
                possiblePos.push(`${row + 1},${col + 2}`);
            }
        }

        if (row - 2 >= 0) {
            if (col - 1 >= 0) {
                possiblePos.push(`${row - 2},${col - 1}`);
            }
            if (col + 1 < 8) {
                possiblePos.push(`${row - 2},${col + 1}`);
            }
        }
        
        if (row + 2 < 8) {
            if (col - 1 >= 0) {
                possiblePos.push(`${row + 2},${col - 1}`);
            }
            if (col + 1 < 8) {
                possiblePos.push(`${row + 2},${col + 1}`);
            }
        }

        
        return possiblePos;

    }

}


export default Knight;
