import React, { Component, useState, useEffect, forwardRef, useImperativeHandle, useRef } from 'react';
import { main, fillGrid } from '../alg/main';
// import { grid } from '../alg/grids';

import './Grid.css';

import congr from '../../../assets/sudoku/congr.mp4';
import lpmainBlue from '../../../assets/lpmain-blue.svg';
import arrowRight from '../../../assets/sudoku/arrow-right.svg';

import Cell from './Cell';

const Square = (props) => {
    const className = "square " + (props.sel ? 'sel ' : ' ') + (props.prefixed ? 'prefixed ' : ' ') + (props.highlighted ? 'highlighted ' : ' ') + (props.numlighted ? 'numlighted ' : ' ') + (props.conflicted ? 'conflicted ' : ' ') + (props.wrong ? 'wrong ' : ' ');
    return (
        <div key={props.id} className={ className } onClick={() => props.selectCell(props.id, props.pos) }>{(props.val != 0 ? props.val : null)}</div>
    );
}

var lastRow = null;
var lastCol = null;
var lastBlock = null;
var lastNum = null;

var count1 = 9;
var count2 = 9;
var count3 = 9;
var count4 = 9;
var count5 = 9;
var count6 = 9;
var count7 = 9;
var count8 = 9;
var count9 = 9;

var undo = [];

const Grid = forwardRef((props, ref) => {

    const [ grid, setGrid ] = useState(props.grid);
    const [ cell, setCell ] = useState(null);
    const board = [];

    const [ solvedGrid, setSolvedGrid ] = useState(props.solvedGrid);

    const [ solved, setSolved ] = useState(false);

    const [time, setTime] = useState(0);
    const [timeCopy, setTimeCopy] = useState(0);

    const vidRef = useRef(null);

    const handleCellChange = (num) => {
        
    }

    const handleKeyPress = (e) => {
        const tempL = [1, 2, 3, 4, 5, 6, 7, 8, 9];

        let key = parseInt(e.key);
        // check if e.key is in tempL
        if (tempL.includes(key)) {
            // console.log('-------')
            props.handleNumClick(key);
        }

        // if (typeof Number(e.key) === 'number' && e.key !== 0) {
        //     console.log(e.key)
        //     props.handleNumClick(e.key);
        // }
    }

    useEffect(() => {
        // window.addEventListener('keypress', (e) => {
        //     const tempL = [1, 2, 3, 4, 5, 6, 7, 8, 9];

        //     let key = parseInt(e.key);
        //     // check if e.key is in tempL
        //     if (tempL.includes(key)) {
        //         // console.log('-------')
        //         props.handleNumClick(key);
        //     }
        // });
        window.addEventListener('keypress', handleKeyPress);
        return () => {
            window.removeEventListener('keypress', handleKeyPress);
        }
    }, []);



    useEffect(() => {
        
        var tempGrid = [];
        for (let i = 0; i < grid.length; i++) {
            let row = [];
            for (let j = 0; j < grid[i].length; j++) {
                let block = Math.floor(i / 3) * 3 + Math.floor(j / 3);
                row.push(new Cell(grid[i][j], (grid[i][j] ? true : false), block));

                if (grid[i][j] === 1) {
                    count1--;
                }
                if (grid[i][j] === 2) {
                    count2--;
                }
                if (grid[i][j] === 3) {
                    count3--;
                }
                if (grid[i][j] === 4) {
                    count4--;
                }
                if (grid[i][j] === 5) {
                    count5--;
                }
                if (grid[i][j] === 6) {
                    count6--;
                }
                if (grid[i][j] === 7) {
                    count7--;
                }
                if (grid[i][j] === 8) {
                    count8--;
                }
                if (grid[i][j] === 9) {
                    count9--;
                }

            }
            tempGrid.push(row);
        }

        props.changeCount1(count1);
        props.changeCount2(count2);
        props.changeCount3(count3);
        props.changeCount4(count4);
        props.changeCount5(count5);
        props.changeCount6(count6);
        props.changeCount7(count7);
        props.changeCount8(count8);
        props.changeCount9(count9);

        setGrid(tempGrid);
        return () => {
            
        }
    }, [])

    // var interval = '';
    useEffect(() => {
        const interval = setInterval(() => {
            setTime(time + 1);
        }, 1000)
        return () => {
            clearInterval(interval);
        }
    }, [time]);

    const highlight = (pos, tempGrid) => {
        
        for (let col = 0; col < grid.length; col++) {
            if (pos[1] !== col) {
                tempGrid[pos[0]][col].highlighted = true;
            }
        }

        for (let row = 0; row < grid.length; row++) {
            if (pos[0] !== row) {
                tempGrid[row][pos[1]].highlighted = true;
            }
        }

        for (let row = 0; row < grid.length; row++) {
            for (let col = 0; col < grid.length; col++) {
                if (tempGrid[row][col].block === tempGrid[pos[0]][pos[1]].block) {
                    tempGrid[row][col].highlighted = true;
                }
            }
        }

        if (grid[pos[0]][pos[1]].val) {
            for (let row = 0; row < grid.length; row++) {
                for (let col = 0; col < grid.length; col++) {
                    if (grid[row][col].val === grid[pos[0]][pos[1]].val && grid[row][col] !== grid[pos[0]][pos[1]]) {
                        grid[row][col].numlighted = true;
                    }
                }
            }
            lastNum = grid[pos[0]][pos[1]].val;
        }

        return tempGrid;
    }

    const selectCell = (id, pos) => {
	    setCell(pos);
        
        props.handleCloseClick();

        var tempGrid = [];
        for (let i = 0; i < grid.length; i++) {
            tempGrid.push(grid[i].slice());
        }

        if (lastRow !== null && lastCol !== null && lastBlock !== null) {
            if (lastRow !== pos[0]) {
                for (let col = 0; col < grid.length; col++) {
                    tempGrid[lastRow][col].highlighted = false;
                }
            }
            if (lastCol !== pos[1]) {
                for (let row = 0; row < grid.length; row++) {
                    tempGrid[row][lastCol].highlighted = false;
                }
            }
            if (lastNum !== null) {
                for (let row = 0; row < grid.length; row++) {
                    for (let col = 0; col < grid.length; col++) {
                        if (grid[row][col].val === lastNum) {
                            grid[row][col].numlighted = false;
                        }
                    }
                }    
            }

            for (let row = 0; row < grid.length; row++) {
                for (let col = 0; col < grid.length; col++) {
                    if (grid[row][col].block === lastBlock) {
                        grid[row][col].highlighted = false;
                    }
                }
            }
            
        }
        
        tempGrid = highlight(pos, tempGrid);

        lastRow = pos[0];
        lastCol = pos[1];
        lastBlock = grid[pos[0]][pos[1]].block;

        setGrid(tempGrid);
    }

    useImperativeHandle(ref, () => ({
        changeCell(num) {
            if (cell && !solved) {
                if (!grid[cell[0]][cell[1]].prefixed) {

                    var tempGrid = [];
                    for (let i = 0; i < grid.length; i++) {
                        tempGrid.push(grid[i].slice());
                    }

                    undo.push(JSON.parse(JSON.stringify(tempGrid)));

                    for (let row = 0; row < grid.length; row++) {
                        for (let col = 0; col < grid.length; col++) {
                            if (cell && grid[row][col].val === grid[cell[0]][cell[1]].val) {
                                tempGrid[row][col].conflicted = false;
                            }
                        }
                    }

                    if (tempGrid[cell[0]][cell[1]].val !== num) {
                        if (num === 1) {
                            count1--;
                            props.changeCount1(count1);
                        }
                        else if (num === 2) {
                            count2--;
                            props.changeCount2(count2);
                        }
                        else if (num === 3) {
                            count3--;
                            props.changeCount3(count3);
                        }
                        else if (num === 4) {
                            count4--;
                            props.changeCount4(count4);
                        }
                        else if (num === 5) {
                            count5--;
                            props.changeCount5(count5);
                        }
                        else if (num === 6) {
                            count6--;
                            props.changeCount6(count6);
                        }
                        else if (num === 7) {
                            count7--;
                            props.changeCount7(count7);
                        }
                        else if (num === 8) {
                            count8--;
                            props.changeCount8(count8);
                        }
                        else if (num === 9) {
                            count9--;
                            props.changeCount9(count9);
                        }

                        if (tempGrid[cell[0]][cell[1]].val === 1) {
                            count1++;
                            props.changeCount1(count1);
                        }
                        else if (tempGrid[cell[0]][cell[1]].val === 2) {
                            count2++;
                            props.changeCount2(count2);
                        }
                        else if (tempGrid[cell[0]][cell[1]].val === 3) {
                            count3++;
                            props.changeCount3(count3);
                        }
                        else if (tempGrid[cell[0]][cell[1]].val === 4) {
                            count4++;
                            props.changeCount4(count4);
                        }
                        else if (tempGrid[cell[0]][cell[1]].val === 5) {
                            count5++;
                            props.changeCount5(count5);
                        }
                        else if (tempGrid[cell[0]][cell[1]].val === 6) {
                            count6++;
                            props.changeCount6(count6);
                        }
                        else if (tempGrid[cell[0]][cell[1]].val === 7) {
                            count7++;
                            props.changeCount7(count7);
                        }
                        else if (tempGrid[cell[0]][cell[1]].val === 8) {
                            count8++;
                            props.changeCount8(count8);
                        }
                        else if (tempGrid[cell[0]][cell[1]].val === 9) {
                            count9++;
                            props.changeCount9(count9);
                        }

                    }

                    tempGrid[cell[0]][cell[1]].val = num;

                    let conflicted = false;

                    for (let row = 0; row < grid.length; row++) {
                        for (let col = 0; col < grid.length; col++) {
                            if (grid[row][col].val && grid[row][col].val === grid[cell[0]][cell[1]].val && grid[row][col] !== grid[cell[0]][cell[1]]) {
                                if (row === cell[0] || col === cell[1] || grid[row][col].block === grid[cell[0]][cell[1]].block) {
                                    tempGrid[row][col].conflicted = true;
                                    conflicted = true;
                                }
                            }
                        }
                    }
                    if (conflicted) {
                        tempGrid[cell[0]][cell[1]].conflicted = true;
                    }

                    if (lastRow !== null && lastCol !== null && lastBlock !== null) {

                        if (lastRow !== cell[0]) {
                            for (let col = 0; col < grid.length; col++) {
                                tempGrid[lastRow][col].highlighted = false;
                            }
                        }
                        if (lastCol !== cell[1]) {
                            for (let row = 0; row < grid.length; row++) {
                                tempGrid[row][lastCol].highlighted = false;
                            }
                        }
                        if (lastNum !== null) {
                            for (let row = 0; row < grid.length; row++) {
                                for (let col = 0; col < grid.length; col++) {
                                    if (grid[row][col].val === lastNum) {
                                        tempGrid[row][col].numlighted = false;
                                    }
                                }
                            }    
                        }
            
                        for (let row = 0; row < grid.length; row++) {
                            for (let col = 0; col < grid.length; col++) {
                                if (grid[row][col].block === lastBlock) {
                                    tempGrid[row][col].highlighted = false;
                                }
                            }
                        }
                        
                        tempGrid[cell[0]][cell[1]].wrong = false;
                    }
            
                    highlight(cell, tempGrid);

                    if (tempGrid[cell[0]][cell[1]].val !== solvedGrid[cell[0]][cell[1]]) {
                        tempGrid[cell[0]][cell[1]].wrong = true;
                    }

                    let solved = true;
                    for (let i = 0; i < grid.length; i++) {
                        for (let j = 0; j < grid.length; j++) {
                            if (!grid[i][j].val || grid[i][j].val !== solvedGrid[i][j]) {
                                solved = false;
                            }
                        }
                    }

                    setGrid(tempGrid);

                    if (solved === true) {
                        vidRef.current.play();
                        setTimeCopy(time);
                        setSolved(true);
                    }

                }
            }
        },
        eraseCell() {
            this.changeCell(null);
            // if (cell) {
            //     if (!grid[cell[0]][cell[1]].prefixed) {
            //         var tempGrid = [];
            //         for (let i = 0; i < grid.length; i++) {
            //             tempGrid.push(grid[i].slice());
            //         }

            //         if (lastRow !== null && lastCol !== null && lastBlock !== null) {

            //             if (lastRow !== cell[0]) {
            //                 for (let col = 0; col < grid.length; col++) {
            //                     tempGrid[lastRow][col].highlighted = false;
            //                 }
            //             }
            //             if (lastCol !== cell[1]) {
            //                 for (let row = 0; row < grid.length; row++) {
            //                     tempGrid[row][lastCol].highlighted = false;
            //                 }
            //             }
            //             if (lastNum !== null) {
            //                 for (let row = 0; row < grid.length; row++) {
            //                     for (let col = 0; col < grid.length; col++) {
            //                         if (grid[row][col].val === lastNum) {
            //                             grid[row][col].numlighted = false;
            //                         }
            //                     }
            //                 }    
            //             }
            
            //         }
            
            //         for (let row = 0; row < grid.length; row++) {
            //             for (let col = 0; col < grid.length; col++) {
            //                 if (grid[row][col].val === grid[cell[0]][cell[1]].val) {
            //                     grid[row][col].conflicted = false;
            //                 }
            //             }
            //         }

            //         let num = grid[cell[0]][cell[1]].val;

            //         if (num === 1) {
            //             count1++;
            //             props.changeCount1(count1);
            //         }
            //         else if (num === 2) {
            //             count2++;
            //             props.changeCount2(count2);
            //         }
            //         else if (num === 3) {
            //             count3++;
            //             props.changeCount3(count3);
            //         }
            //         else if (num === 4) {
            //             count4++;
            //             props.changeCount4(count4);
            //         }
            //         else if (num === 5) {
            //             count5++;
            //             props.changeCount5(count5);
            //         }
            //         else if (num === 6) {
            //             count6++;
            //             props.changeCount6(count6);
            //         }
            //         else if (num === 7) {
            //             count7++;
            //             props.changeCount7(count7);
            //         }
            //         else if (num === 8) {
            //             count8++;
            //             props.changeCount8(count8);
            //         }
            //         else if (num === 9) {
            //             count9++;
            //             props.changeCount9(count9);
            //         }

            //         tempGrid[cell[0]][cell[1]].val = null;

            //         setGrid(tempGrid);
            //     }
            // }
        },
        hintCell() {
            this.changeCell(solvedGrid[cell[0]][cell[1]]);
            // if (cell) {
            //     if (!grid[cell[0]][cell[1]].prefixed) {
            //         var tempGrid = [];
            //         for (let i = 0; i < grid.length; i++) {
            //             tempGrid.push(grid[i].slice());
            //         }

            //         if (lastNum !== null) {
            //             for (let i = 0; i < tempGrid.length; i++) {
            //                 for (let j = 0; j < tempGrid.length; j++) {
            //                     if (tempGrid.val === lastNum) {
            //                         tempGrid.conflicted = false;
            //                     }
            //                 }
            //             }
    
            //         }
            //         tempGrid[cell[0]][cell[1]].wrong = false;

            //         tempGrid[cell[0]][cell[1]].val = solvedGrid[cell[0]][cell[1]];

            //         let solved = true;
            //         for (let i = 0; i < grid.length; i++) {
            //             for (let j = 0; j < grid.length; j++) {
            //                 if (!grid[i][j].val || grid[i][j].val !== solvedGrid[i][j]) {
            //                     solved = false;
            //                 }
            //             }
            //         }

            //         setGrid(tempGrid);

            //         if (solved === true) {
            //             setSolved(true);
            //             vidRef.current.play();
            //         }

            //     }
            // }
        },
        updateGrid(sgrid, pgrid) {

            undo = [];

            vidRef.current.pause();
            vidRef.current.load();
            
            count1 = 9;
            count2 = 9;
            count3 = 9;
            count4 = 9;
            count5 = 9;
            count6 = 9;
            count7 = 9;
            count8 = 9;
            count9 = 9;

            var tempGrid = [];
            for (let i = 0; i < pgrid.length; i++) {
                let row = [];
                for (let j = 0; j < pgrid[i].length; j++) {
                    let block = Math.floor(i / 3) * 3 + Math.floor(j / 3);
                    row.push(new Cell(pgrid[i][j], (pgrid[i][j] ? true : false), block));
    
                    if (pgrid[i][j] === 1) {
                        count1--;
                    }
                    if (pgrid[i][j] === 2) {
                        count2--;
                    }
                    if (pgrid[i][j] === 3) {
                        count3--;
                    }
                    if (pgrid[i][j] === 4) {
                        count4--;
                    }
                    if (pgrid[i][j] === 5) {
                        count5--;
                    }
                    if (pgrid[i][j] === 6) {
                        count6--;
                    }
                    if (pgrid[i][j] === 7) {
                        count7--;
                    }
                    if (pgrid[i][j] === 8) {
                        count8--;
                    }
                    if (pgrid[i][j] === 9) {
                        count9--;
                    }
    
                }
                tempGrid.push(row);
            }
    
            setSolved(false);
            setGrid(tempGrid);
            setSolvedGrid(sgrid);
            
            setTime(0);
            setTimeCopy(0);
            setCell(null);

            props.changeCount1(count1);
            props.changeCount2(count2);
            props.changeCount3(count3);
            props.changeCount4(count4);
            props.changeCount5(count5);
            props.changeCount6(count6);
            props.changeCount7(count7);
            props.changeCount8(count8);
            props.changeCount9(count9);
    
        },
        undoGrid() {
            if (undo.length > 0 && !solved) {

                count1 = 9;
                count2 = 9;
                count3 = 9;
                count4 = 9;
                count5 = 9;
                count6 = 9;
                count7 = 9;
                count8 = 9;
                count9 = 9;

                let tempGrid = undo[undo.length - 1];
                for (let i = 0; i < tempGrid.length; i++) {
                    for (let j = 0; j < tempGrid[i].length; j++) {
                        tempGrid[i][j].highlighted = false;
                        tempGrid[i][j].numlighted = false;

                        if (tempGrid[i][j].val === 1) {
                            count1--;
                        }
                        if (tempGrid[i][j].val === 2) {
                            count2--;
                        }
                        if (tempGrid[i][j].val === 3) {
                            count3--;
                        }
                        if (tempGrid[i][j].val === 4) {
                            count4--;
                        }
                        if (tempGrid[i][j].val === 5) {
                            count5--;
                        }
                        if (tempGrid[i][j].val === 6) {
                            count6--;
                        }
                        if (tempGrid[i][j].val === 7) {
                            count7--;
                        }
                        if (tempGrid[i][j].val === 8) {
                            count8--;
                        }
                        if (tempGrid[i][j].val === 9) {
                            count9--;
                        }

                    }
                }
                
                props.changeCount1(count1);
                props.changeCount2(count2);
                props.changeCount3(count3);
                props.changeCount4(count4);
                props.changeCount5(count5);
                props.changeCount6(count6);
                props.changeCount7(count7);
                props.changeCount8(count8);
                props.changeCount9(count9);
    
                setGrid(undo[undo.length - 1]);

                highlight(cell, undo[undo.length - 1]);
                undo.pop();
            }

        },
    }))

    for (let i = 0; i < 9; ++i) {
        const row = [];
        for (let j = 0; j < 9; ++j) {
            row.push(<Square id={String(i) + String(j)} pos={[i, j]} selectCell={ selectCell } val={grid[i][j].val} prefixed={grid[i][j].prefixed} highlighted={grid[i][j].highlighted} numlighted={grid[i][j].numlighted} conflicted={grid[i][j].conflicted} wrong={grid[i][j].wrong} sel={(cell ? (String(cell[0]) + String(cell[1]) === String(i) + String(j) ? true : false) : false)} />);
        }
        board.push(row);
    }

    return (
        <div id='gridcont'>
            <label id='timelabel' style={(solved ? {top: '10px'} : {})}>{(time > 3600 ? new Date(time * 1000).toISOString().substr(11, 8) : new Date(time * 1000).toISOString().substr(14, 5))}</label>
            <label id='newgamelabel'>New game</label>
            <label id='difflabel' style={(solved ? {top: '10px'} : {})}>{ props.diff }</label>
            <div id="grid" style={(solved ? {borderRadius: '30px', border: '4px solid white'} : {})}>
                {board.map((row) => {
                    return (
                        <div className='gridRow'>
                            {row.map((cell) => {
                                return (
                                    cell
                                )
                            })}
                        </div>
                    )
                })}

                <div id='gridsolved' style={{backgroundImage: 'url(' + lpmainBlue + ')'}} className={(solved ? 'solved' : '')}>
                    <div id='gscont'>

                        <div id='gstop'>
                            <label id='gsmainlabel' htmlFor="">Congratulations</label>
                            <div id='gsstatcont'>
                                <div className='gsstat'>
                                    <label htmlFor="">Difficulty</label>
                                    <img src={ arrowRight } alt="" />
                                    <label htmlFor="">{ props.diff }</label>
                                </div>
                                <div className='gsstat'>
                                    <label htmlFor="">Time</label>
                                    <img src={ arrowRight } alt="" />
                                    <label>{( solved ? (timeCopy > 3600 ? new Date(timeCopy * 1000).toISOString().substr(11, 8) : new Date(timeCopy * 1000).toISOString().substr(14, 5)) : '')}</label>
                                    {/* <label htmlFor="">{(time > 3600 ? new Date(time * 1000).toISOString().substr(11, 8) : new Date(time * 1000).toISOString().substr(14, 5))}</label> */}
                                </div>
                            </div>
                        </div>
                        <video src={ congr } loop ref={vidRef}></video>

                    </div>
                </div>
            </div>
        </div>
    );

})

export default Grid;